import React, {useGlobal,useEffect} from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {useHistory} from "react-router-dom";
import BackButton from '../../../backButton';
import {MOUTHFEEL,ACIDITY,CARAMEL,FRUIT,FLOWER,VARIETAL} from '../../../../constant/learning';
import sliderValues from './steps';
import Quality from '../quality';
import text from '../text';
const Flower=(props)=>{
	const classes = useStyles(),
	history=useHistory(),
	[varietal]= useGlobal(VARIETAL),
	[mouthFeel]= useGlobal(MOUTHFEEL),
	[acidity, ]= useGlobal(ACIDITY),
	[caramel,]=useGlobal(CARAMEL),
	[fruit,]=useGlobal(FRUIT),
	[flower,setFlower]=useGlobal(FLOWER),
	onClick=(flower)=>{
		history.push('/learning/profile')
		setFlower(flower);
	};
	useEffect(()=>{
		if(!varietal || !mouthFeel || !acidity || !caramel || !fruit){
			history.push('/learning')
		};
	},[]);
	return (
		<div className={classes.page}>
			<Grid container className={classes.header}>
				<BackButton />
			</Grid>
			<Typography 
				variant='h5' 
				gutterBottom
				align='center'>
					{varietal&&`${varietal.region}-${varietal.roast}`}
			</Typography>
			<Quality
				value={flower}
				title='🚫 Flower 🌹'
				text={text.flower}
				values={sliderValues}
				onClick={onClick}/>

		</div>)
};
export default Flower;
