import React, { useState, useEffect, useGlobal } from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import { USER } from '../../../../constant/coffee';
//import text from '../text';
import text from '../text';
const ContactInfo = (props) => {
  const { onValid } = props;
  const classes = useStyles();
  const [firstName, setFirstName] = useState('');
  const [middleName, setMiddleName] = useState('');
  const [lastName, setLastName] = useState('');
  const [businessName, setBusinessName] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');
  const [user] = useGlobal(USER);
  const onPhoneChange = (event) => {
    setPhone(event.target.value);
  };
  const onEmailChange = (event) => {
    setEmail(event.target.value);
  };
  const onFirstNameChange = (event) => {
    setFirstName(event.target.value);
  };
  const onMiddleNameChange = (event) => {
    setMiddleName(event.target.value);
  };
  const onLastNameChange = (event) => {
    setLastName(event.target.value);
  };
  const onBusinessNameChange = (event) => {
    setBusinessName(event.target.value);
  };
  const testPhone = () => {
    return (
      phone && /^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/.test(phone)
    );
  };
  const testEmail = () => {
    return true;
  };
  const testFirstName = () => {
    return firstName && firstName.length > 2;
  };
  const testLastName = () => {
    return lastName && lastName.length > 2;
  };
  const testAll = () => {
    return testPhone() && testFirstName() && testLastName();
  };
  const valid = () => {
    if (onValid) {
      if (testAll()) {
        onValid({
          phone,
          firstName,
          middleName,
          lastName,
          businessName,
          email,
        });
      } else {
        onValid(null);
      }
    }
  };
  const renderInputs = () => {
    return (
      <Paper elevation={2} className={classes.inputs}>
        <Typography variant="h6">Contact Information</Typography>
        <Paper elevation={0} className={classes.customerNames}>
          <TextField
            required
            autoComplete="given-name"
            error={!testFirstName()}
            value={firstName}
            label={text.contactInfo.firstName}
            className={classes.textField}
            margin="normal"
            onChange={onFirstNameChange}
          />
          <TextField
            value={middleName}
            autoComplete="additional-name"
            label={text.contactInfo.middleName}
            className={classes.textField}
            margin="normal"
            onChange={onMiddleNameChange}
          />
          <TextField
            required
            autoComplete="family-name"
            error={!testLastName()}
            value={lastName}
            label={text.contactInfo.lastName}
            className={classes.textField}
            margin="normal"
            onChange={onLastNameChange}
          />
        </Paper>
        <TextField
          value={businessName}
          label={text.contactInfo.businessName}
          className={classes.textField}
          margin="normal"
          onChange={onBusinessNameChange}
        />
        <TextField
          error={!testPhone()}
          type="tel"
          value={phone}
          label={text.contactInfo.phone}
          className={classes.textField}
          margin="normal"
          onChange={onPhoneChange}
        />
        <TextField
          error={!testEmail()}
          type="email"
          value={email}
          label={text.contactInfo.email}
          className={classes.textField}
          margin="normal"
          onChange={onEmailChange}
        />
      </Paper>
    );
  };
  useEffect(() => {
    valid();
  }, [firstName, middleName, lastName, businessName, phone, email]);
  useEffect(() => {
    if (user && user.contactInfo) {
      const { firstName, middleName, lastName, businessName, phone, email } =
        user.contactInfo;
      firstName && setFirstName(firstName);
      middleName && setMiddleName(middleName);
      lastName && setLastName(lastName);
      businessName && setBusinessName(businessName);
      phone && setPhone(phone);
      email && setEmail(email);
    }
  }, [user]);
  return (
    <Grid
      container
      direction="column"
      spacing={0}
      wrap="nowrap"
      className={classes.content}
    >
      {renderInputs()}
    </Grid>
  );
};
export default ContactInfo;
