import React, { useGlobal, useState, useEffect } from 'reactn';
import { useLazyQuery, gql } from '@apollo/client';
import InputAdornment from '@material-ui/core/InputAdornment';
import EmailIcon from '@material-ui/icons/EmailTwoTone';
import PasswordIcon from '@material-ui/icons/LockOpenTwoTone';
import CircularProgress from '@material-ui/core/CircularProgress';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import { useHistory } from 'react-router-dom';
import BackButton from '../../../backButton';
import { USER } from '../../../../constant/coffee';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Typography } from '@material-ui/core';
import UseApolloError from '../../../handleError/useApolloError';
import GoogleLogin from 'react-google-login';
import FacebookLogin from 'react-facebook-login';
import text from '../text';
import { clientId } from '../../../../constant/googleLogin';
const SIGNIN = gql`
  query ($email: String, $password: String) {
    signin(email: $email, password: $password) {
      name
      _id
      address {
        city
        street
        suite
        zipcode
        state
        country
      }
      contactInfo {
        phone
        firstName
        lastName
        businessName
        email
      }
      token
    }
  }
`;

const GOOGLE_SIGNIN = gql`
  query ($token: String) {
    googleSignin(token: $token) {
      name
      _id
      address {
        city
        street
        suite
        zipcode
        state
        country
      }
      contactInfo {
        phone
        firstName
        lastName
        middleName
        businessName
        email
      }
      token
    }
  }
`;

const UserSignin = (props) => {
  const apolloError = UseApolloError();
  const classes = useStyles();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [doSignin, { loading, error, data }] = useLazyQuery(SIGNIN);
  const [
    doGoogleSignin,
    { loading: googleLoading, error: googleError, data: googleData },
  ] = useLazyQuery(GOOGLE_SIGNIN);
  const [user, setUser] = useGlobal(USER);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [dialogMsg, setDialogMsg] = useState();
  const [showProgress, setShowProgress] = useState(false);
  const history = useHistory();
  const onEmailChange = (event) => {
    setEmail(event.target.value.toLocaleLowerCase());
  };
  const onPasswordChange = (event) => {
    setPassword(event.target.value);
  };
  const handleDlogClose = () => {
    setDialogOpen(false);
  };
  const onCancelClick = () => {
    setDialogOpen(false);
    history.goBack();
  };
  const onForgotPasswordClick = () => {
    history.push('/user/forgotPassword');
  };
  const onClick = () => {
    const lowerCaseEmail = email.toLocaleLowerCase();
    doSignin({ variables: { email: lowerCaseEmail, password: password } });
  };
  const testEmail = () => {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  };
  const testPassword = () => {
    return /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])([a-zA-Z0-9]{8,})$/.test(
      password
    );
    //	return password.length > 2;
  };
  const renderDialog = () => {
    return (
      <Dialog
        open={dialogOpen}
        onClose={handleDlogClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="join-dialog-description"
      >
        <DialogTitle id="join-dialog-title">
          {'We encountered a problem'}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="join-dialog-description">
            {dialogMsg}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDlogClose} color="primary">
            Try again
          </Button>
        </DialogActions>
      </Dialog>
    );
  };
  const renderInputs = () => {
    return (
      <Paper elevation={2} className={classes.inputs}>
        <Paper elevation={2} className={classes.logins}>
          {renderGoogle()}
          {/* renderFacebook() */}
        </Paper>
        <Typography
          variant="subtitle1"
          className={classes.forgotPasswordLink}
          onClick={onForgotPasswordClick}
        >
          Forgot your password?
        </Typography>
        <TextField
          required
          error={!testEmail()}
          helperText={text.signin.emailHelpText}
          value={email}
          type="email"
          label={text.signin.emailLabel}
          className={classes.textField}
          onChange={onEmailChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <EmailIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          value={password}
          label="Password"
          autoComplete="current-password"
          error={!testPassword()}
          helperText={text.signin.passwordHelpText}
          required
          className={classes.textField}
          type={text.signin.passwordLabel}
          margin="normal"
          onChange={onPasswordChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <PasswordIcon />
              </InputAdornment>
            ),
          }}
        />
        <Grid container className={classes.buttons}>
          <Button variant="contained" onClick={onCancelClick} color="primary">
            {text.signin.cancelButton}
          </Button>
          <Button
            variant="contained"
            disabled={!(testPassword() && testEmail())}
            onClick={onClick}
            color="primary"
          >
            {text.signin.golButton}
          </Button>
        </Grid>
      </Paper>
    );
  };
  const responseSuccessGoogle = (r) => {
    doGoogleSignin({ variables: { token: r.tokenId } });
  };
  const responseErrorGoogle = (code, details) => {
    setDialogOpen(true);
    console.log('responseErrorGoogle', code, details);
    setDialogMsg(`Google Login Failed ${(code, details)}`);
  };
  const responseFacebook = (response) => {
    debugger;
  };
  const renderGoogle = () => {
    return (
      <GoogleLogin
        clientId={clientId}
        buttonText="Login with Google"
        onSuccess={responseSuccessGoogle}
        onFailure={responseErrorGoogle}
        cookiePolicy={'single_host_origin'}
      />
    );
  };
  const renderFacebook = () => {
    return (
      <FacebookLogin appId="379347720540800" callback={responseFacebook} />
    );
  };
  const handleSignin = () => {
    console.log('handle Signin');
    setShowProgress(false);
    if (data) {
      if (data.signin) {
        const signin = { ...data.signin };
        signin.contactInfo = { ...signin.contactInfo };
        signin.address = { ...signin.address };
        delete signin.__typename;
        delete signin.contactInfo.__typename;
        delete signin.address.__typename;
        localStorage.setItem('auth_token', signin.token);
        delete signin.token;
        setUser(signin);
        setDialogOpen(false);
        history.goBack();
      } else {
        setDialogOpen(true);
        setDialogMsg(text.signin.failedMsg);
      }
    }
  };
  const handleGoogleSignin = () => {
    console.log('handle Signin');
    if (googleData) {
      setShowProgress(false);
      if (googleData.googleSignin) {
        const signin = { ...googleData.googleSignin };
        signin.contactInfo = { ...signin.contactInfo };
        signin.address = { ...signin.address };
        delete signin.__typename;
        delete signin.contactInfo.__typename;
        delete signin.address.__typename;
        localStorage.setItem('auth_token', signin.token);
        delete signin.token;
        setUser(signin);
        setDialogOpen(false);
        history.goBack();
      } else {
        setDialogOpen(true);
        setDialogMsg(text.signin.failedMsg);
      }
    }
  };
  useEffect(() => {
    if (googleData) {
      handleGoogleSignin();
    }
  }, [googleData]);

  useEffect(() => {
    if (googleLoading) setShowProgress(true);
  }, [googleLoading]);

  useEffect(() => {
    if (googleError) {
      debugger;
      setUser(null);
      setShowProgress(false);
      const error = { message: text.signin.errorMsg };
      apolloError(error);
    }
  }, [googleError]);

  useEffect(handleSignin, [data]);
  useEffect(() => {
    if (error) {
      debugger;
      setUser(null);
      setShowProgress(false);
      const err = { message: text.signin.inputIncorrectMsg };
      apolloError(error);
    }
  }, [error]);
  return (
    <>
      {showProgress && <CircularProgress className={classes.progress} />}
      {renderDialog()}
      <Grid container className={classes.header}>
        <BackButton />
      </Grid>
      <Grid
        container
        direction="column"
        spacing={0}
        wrap="nowrap"
        className={classes.content}
      >
        {renderInputs()}
      </Grid>
    </>
  );
};
export default UserSignin;
