import React, { useGlobal, useEffect, useState } from 'reactn';
import { useMutation, gql } from '@apollo/client';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { USER } from '../../../../constant/coffee';
import { useHistory, useLocation } from 'react-router-dom';

//https://www.purfectblend.com/user/verify?token=619a86c659dbad0ee0c07fb3&email=purfectblend@gmail.com

const VERIFY_USER = gql`
  mutation verifyUser($verifyUser: VerifyUser) {
    verifyUser(verifyUser: $verifyUser) {
      requestStatus {
        success
        code
        message
      }
      name
      _id
      address {
        city
        street
        suite
        zipcode
        state
        country
      }
      contactInfo {
        phone
        firstName
        lastName
        businessName
        email
      }
    }
  }
`;
const Verify = (props) => {
  const location = useLocation();
  const classes = useStyles();
  const history = useHistory();
  const [verifyUser, { loading }] = useMutation(VERIFY_USER, {
    onCompleted(data) {
      // setShowProgress(false);
      const { verifyUser } = data;
      const { requestStatus } = verifyUser;
      if (requestStatus.success === true) {
        delete data.verifyUser.requestStatus;
        setUser(verifyUser);
        history.push('/user/welcome');
      } else {
        console.log(data);
        debugger;
      }
    },
    onError: (err) => {
      //  setShowProgress(false);
      console.log(err);
      debugger;
    },
  });
  const [user, setUser] = useGlobal(USER);
  const [token, setToken] = useState();
  const [email, setEmail] = useState();
  const onClick = () => {
    history.push('/');
  };
  const renderInputs = () => {
    return (
      <Paper elevation={2} className={classes.inputs}>
        <Typography variant="body1">Welcome {user.name}</Typography>
        <Button variant="contained" onClick={onClick} color="primary">
          go
        </Button>
      </Paper>
    );
  };
  useEffect(() => {
    const urlSearch = new URLSearchParams(location.search);
    const token = urlSearch.get('token');
    const email = urlSearch.get('email');
    setToken(token);
    setEmail(email);
  }, [location]);
  useEffect(() => {
    if (email && token) {
      verifyUser({ variables: { verifyUser: { email, token } } });
    }
  }, [email, token]);
  return (
    <div className={classes.page}>
      <Grid
        container
        direction="column"
        spacing={0}
        wrap="nowrap"
        className={classes.content}
      >
        test
      </Grid>
    </div>
  );
};
export default Verify;
