import React, { useGlobal, useEffect } from 'reactn';
import useStyles from './styles';
import { useHistory } from 'react-router-dom';
import { Grid, Typography, Button, Paper } from '@material-ui/core';
import BackButton from '../../../backButton';
import UserButtons from '../../user/buttons';
import MenuBar from '../../../menuBar';
import {
  OUR_BLENDS_SHOPPING_CART,
  COFFEES_SHOPPING_CART,
  BLEND_SHOPPING_CART,
  USER,
} from '../../../../constant/coffee';
import HeaderImg from '../../../headerImg';
import CoffeeItems from './coffeeItems';
import BlendItems from './blendItems';
import OurBlendItems from './ourBlendItems';
import Copyright from '../../../copyright';

const ShoppingCart = () => {
  const classes = useStyles();
  const history = useHistory();
  const [ourBlendsShoppingCart] = useGlobal(OUR_BLENDS_SHOPPING_CART);
  const [coffeeShoppingCart] = useGlobal(COFFEES_SHOPPING_CART);
  const [blendShoppingCart] = useGlobal(BLEND_SHOPPING_CART);
  const [user] = useGlobal(USER);
  const onCheckout = () => {
    history.push('/buy/checkout', { next: 'coffees' });
  };
  const calculateTotal = () => {
    const blendTotal = blendShoppingCart.reduce((a, purchase) => {
      return (a += purchase.price.price * purchase.quantity);
    }, 0);
    const filteredCoffees = coffeeShoppingCart.filter((p) => p.quantity > 0);
    let coffeeTotal = 0;
    filteredCoffees.forEach((purchase) => {
      coffeeTotal += purchase.price.price * purchase.quantity;
    });
    const filteredOurBlends = ourBlendsShoppingCart.filter(
      (p) => p.quantity > 0
    );
    let ourBlendTotal = 0;
    filteredOurBlends.forEach((purchase) => {
      ourBlendTotal += purchase.price.price * purchase.quantity;
    });
    return coffeeTotal + blendTotal + ourBlendTotal;
  };
  const renderTotal = () => {
    return (
      <Paper elevation={4} className={classes.totalContainer}>
        {user ? (
          <Typography variant="h6" className={classes.userName}>
            {user.name}
          </Typography>
        ) : null}
        <Typography variant="h6" className={classes.total}>
          your total is
        </Typography>
        <Typography variant="h6" className={classes.total}>
          {new Intl.NumberFormat('en-US', {
            style: 'currency',
            currency: 'USD',
          }).format(calculateTotal())}
        </Typography>
      </Paper>
    );
  };
  const coffeeQty = () => {
    if (!coffeeShoppingCart) return 0;
    let q = 0;
    coffeeShoppingCart.forEach((p) => {
      if (p.quantity > 0) q++;
    });
    return q;
  };
  const ourBlendsQty = () => {
    if (!ourBlendsShoppingCart) return 0;
    let q = 0;
    ourBlendsShoppingCart.forEach((p) => {
      if (p.quantity > 0) q++;
    });
    return q;
  };
  const validBlendShoppingCart = () => {
    if (!blendShoppingCart) return false;
    if (blendShoppingCart.length === 0) return false;
    return true;
  };
  useEffect(() => {
    if (
      coffeeQty() === 0 &&
      !validBlendShoppingCart() &&
      ourBlendsQty() === 0
    ) {
      history.push('/');
    }
  });
  return coffeeQty() === 0 &&
    !validBlendShoppingCart() &&
    ourBlendsQty() === 0 ? null : (
    <div className={classes.page}>
      <div className={classes.userButtons}>
        <UserButtons />
      </div>
      <Grid
        container
        direction="column"
        spacing={0}
        wrap="nowrap"
        className={classes.content}
      >
        <div className={classes.menubar}>
          <BackButton />
          <MenuBar dept="all" />
        </div>
        <HeaderImg size="small" />
        <Button
          variant="contained"
          color="primary"
          disabled={!user}
          size="large"
          className={classes.button}
          onClick={onCheckout}
        >
          check out
        </Button>
        {renderTotal()}
        <BlendItems />
        <OurBlendItems />
        <CoffeeItems />
      </Grid>
      <Copyright />
    </div>
  );
};
export default ShoppingCart;
