import React, { useState, useGlobal, useEffect } from 'reactn';
import ReCAPTCHA from 'react-google-recaptcha';
import CaptchaKey from '../../../../constant/captcha';
import CircularProgress from '@material-ui/core/CircularProgress';
import useStyles from './styles';
import InputAdornment from '@material-ui/core/InputAdornment';
import EmailIcon from '@material-ui/icons/EmailTwoTone';
import PasswordIcon from '@material-ui/icons/LockOpenTwoTone';
import UserIcon from '@material-ui/icons/AccountCircleTwoTone';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import { useHistory } from 'react-router-dom';
import BackButton from '../../../backButton';
import { USER } from '../../../../constant/coffee';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { useLazyQuery, useMutation, gql } from '@apollo/client';
import UseApolloError from '../../../handleError/useApolloError';
import GoogleLogin from 'react-google-login';
import { clientId } from '../../../../constant/googleLogin';
import text from '../text';
const JOIN = gql`
  mutation useJoin($userInput: UserInput) {
    userJoin(user: $userInput) {
      success
      code
      message
    }
  }
`;
const GOOGLE_SIGNIN = gql`
  query ($token: String) {
    googleSignin(token: $token) {
      name
      _id
      address {
        city
        street
        suite
        zipcode
        state
        country
      }
      contactInfo {
        phone
        firstName
        lastName
        businessName
        email
      }
      token
    }
  }
`;
const UserJoin = () => {
  const apolloError = UseApolloError();
  const [dialogOpen, setDialogOpen] = useState(false);
  const [success, setSuccess] = useState(false);
  const [showProgress, setShowProgress] = useState(false);
  const [dialogMsg, setDialogMsg] = useState('');
  const classes = useStyles();
  const [
    doGoogleSignin,
    { loading: googleLoading, error: googleError, data: googleData },
  ] = useLazyQuery(GOOGLE_SIGNIN);
  const [doJoin, { loading }] = useMutation(JOIN, {
    onCompleted(data) {
      setShowProgress(false);
      const { success, code, message } = data.userJoin;
      setSuccess(success);
      if (success) {
        setDialogMsg(
          'A verification email has been sent to you. Please verify your email address'
        );
      } else {
        if (code === '460') {
          setDialogMsg('user with this email already exists');
        }
      }
      setDialogOpen(true);
    },
    onError: (err) => {
      debugger;
      setShowProgress(false);
      setDialogMsg(
        'There was a problem with the server. Please try again later'
      );
      setDialogOpen(true);
    },
  });
  const history = useHistory();
  const [user, setUser] = useGlobal(USER);
  const [email, setEmail] = useState('');
  const [captcha, setCaptcha] = useState(false);
  const [password, setPassword] = useState('');
  const [repeatPassword, setRepeatPassword] = useState('');
  const [name, setName] = useState('');
  const onEmailChange = (event) => {
    setEmail(event.target.value.toLocaleLowerCase());
  };
  const onPasswordChange = (event) => {
    setPassword(event.target.value);
  };
  const onRepeatPasswordChange = (event) => {
    setRepeatPassword(event.target.value);
  };
  const onNameChange = (event) => {
    setName(event.target.value);
  };
  const onRecaptchaChange = (value) => {
    setCaptcha(value != null ? true : false);
  };
  const onDialogClick = () => {
    setDialogOpen(false);
    history.goBack();
  };
  const onCancelClick = () => {
    history.goBack();
  };
  const onClick = () => {
    setShowProgress(true);
    const lowerCaseEmail = email.toLocaleLowerCase();
    doJoin({
      variables: {
        userInput: {
          name: name,
          credentals: {
            email: lowerCaseEmail,
            password: password,
            name: name,
          },
        },
      },
    });
  };
  const testEmail = () => {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  };
  const testPassword = () => {
    //return password.length>2
    return /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])([a-zA-Z0-9]{8,})$/.test(
      password
    );
  };
  const testRepeatPassword = () => {
    return repeatPassword.length > 0 && password === repeatPassword;
  };
  const handleDlogClose = () => {
    setDialogOpen(false);
    if (success) {
      history.push('/');
    } else {
    }
  };
  const renderDialog = () => {
    return dialogOpen ? (
      <Dialog
        open={dialogOpen}
        onClose={handleDlogClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="join-dialog-description"
      >
        <DialogTitle id="join-dialog-title">
          {'We encountered a problem'}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="join-dialog-description">
            {dialogMsg}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDlogClose} color="primary">
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    ) : null;
  };
  const renderInputs = () => {
    return (
      <Paper elevation={2} className={classes.inputs}>
        {renderGoogle()}

        <TextField
          required
          autoComplete="username"
          error={!testEmail()}
          helperText="enter a valid email"
          value={email}
          label="email"
          type="email"
          className={classes.textField}
          margin="normal"
          onChange={onEmailChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <EmailIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          value={password}
          label="Password"
          error={!testPassword()}
          helperText="at least 8 characters, 1 number, 1 upper and 1 lowercase"
          required
          className={classes.textField}
          type="password"
          autoComplete="new-password"
          margin="normal"
          onChange={onPasswordChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <PasswordIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          value={repeatPassword}
          label="Repeat Password"
          error={!testRepeatPassword()}
          helperText="passwords must match"
          required
          className={classes.textField}
          type="password"
          margin="normal"
          onChange={onRepeatPasswordChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <PasswordIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          value={name}
          label="User name"
          autoComplete="nickname"
          error={!name.length}
          required
          className={classes.textField}
          type="text"
          margin="normal"
          onChange={onNameChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <UserIcon />
              </InputAdornment>
            ),
          }}
        />
        <ReCAPTCHA
          className={classes.recaptcha}
          sitekey={CaptchaKey}
          onChange={onRecaptchaChange}
        />
        <Grid container className={classes.buttons}>
          <Button variant="contained" onClick={onCancelClick} color="primary">
            cancel
          </Button>
          <Button
            variant="contained"
            disabled={!(captcha && testPassword() && testEmail())}
            onClick={onClick}
            color="primary"
          >
            go
          </Button>
        </Grid>
      </Paper>
    );
  };
  const responseSuccessGoogle = (r) => {
    doGoogleSignin({ variables: { token: r.tokenId } });
  };
  const responseErrorGoogle = (r, x) => {
    setDialogOpen(true);
    setDialogMsg('Google Login Failed');
  };
  const renderGoogle = () => {
    return (
      <GoogleLogin
        clientId={clientId}
        buttonText="Login with Google"
        onSuccess={responseSuccessGoogle}
        onFailure={responseErrorGoogle}
        cookiePolicy={'single_host_origin'}
      />
    );
  };
  const handleGoogleSignin = () => {
    console.log('handle Signin');
    setShowProgress(false);
    if (googleData) {
      if (googleData.googleSignin) {
        const signin = { ...googleData.googleSignin };
        signin.contactInfo = { ...signin.contactInfo };
        signin.address = { ...signin.address };
        delete signin.__typename;
        delete signin.contactInfo.__typename;
        delete signin.address.__typename;
        localStorage.setItem('auth_token', signin.token);
        delete signin.token;
        setUser(signin);
        setDialogOpen(false);
        history.goBack();
      } else {
        // 		setUser(null);
        setDialogOpen(true);
        setDialogMsg(text.signin.failedMsg);
      }
    }
  };
  useEffect(() => {
    if (googleData) {
      handleGoogleSignin();
    }
  }, [googleData]);

  useEffect(() => {
    if (googleLoading) setShowProgress(true);
  }, [googleLoading]);

  useEffect(() => {
    if (googleError) {
      setShowProgress(false);
      setUser(null);
      const error = { message: text.signin.errorMsg };
      apolloError(error);
    }
  }, [googleError]);
  return (
    <div className={classes.page}>
      {showProgress && <CircularProgress className={classes.progress} />}

      {renderDialog()}
      <Grid container className={classes.header}>
        <BackButton />
      </Grid>
      <Grid
        container
        direction="column"
        spacing={0}
        wrap="nowrap"
        className={classes.content}
      >
        {renderInputs()}
      </Grid>
    </div>
  );
};
export default UserJoin;
