import React, { useState, useGlobal } from 'reactn';
import { useHistory } from 'react-router';
import { Dialog, DialogTitle, DialogActions, Button } from '@material-ui/core';
import { USER } from '../../../../constant/coffee';
const UseUserLoginError = () => {
  const history = useHistory();
  const [user, setUser] = useGlobal(USER);
  const [dialogOpen, setDialogOpen] = useState(false);
  const onClick = () => {
    setUser(null);
    localStorage.removeItem('auth_token');
    setDialogOpen(false);
    history.push('/user/signin');
  };
  const renderDialog = () => {
    return dialogOpen ? (
      <Dialog
        open={dialogOpen}
        onClose={() => setDialogOpen(false)}
        aria-labelledby="alert-dialog-title"
      >
        <DialogTitle id="remove-dialog-title">
          You've timed out. Please login back in.
        </DialogTitle>
        <DialogActions>
          <Button onClick={onClick} color="primary">
            OK
          </Button>
        </DialogActions>
      </Dialog>
    ) : null;
  };
  const userLoginError = (error) => {
    if (
      error &&
      error.networkError &&
      error.networkError.result &&
      error.networkError.result.errors
    ) {
      const result =
        error.networkError.result.errors[0].extensions.code ===
        'UNAUTHENTICATED'
          ? true
          : false;
      if (result && dialogOpen === false) setDialogOpen(result);
      return renderDialog();
    }
  };
  return userLoginError;
};

export default UseUserLoginError;
