import React from 'reactn';
import Fab from '@material-ui/core/Fab';
import { FacebookProvider, Share } from 'react-facebook';
import AppId from '../../../constant/facebook'
import useStyles from './styles';
import FacebookIcon from '@material-ui/icons/Facebook';

const Button=(props)=>{
    const classes = useStyles(),
    {onClick,} = props;

	return (<FacebookProvider appId={AppId} className={classes.provider}>
        <Share href="http://www.facebook.com" >
          {({ handleClick, loading }) => (
              <Fab
              className={classes.fab}
              aria-label="facebook"
              size='small'
              disabled={loading}
              onClick={handleClick} >
              <FacebookIcon  className={classes.icon} onClick={onClick}/>
          </Fab>
          )}
        </Share>


    </FacebookProvider>)
};
export default Button;

