import React, { useState, useEffect, useGlobal } from 'reactn';
import InputAdornment from '@material-ui/core/InputAdornment';
import OTPIcon from '@material-ui/icons/EmailTwoTone';
import useStyles from './styles';
import { USER } from '../../../../../../constant/coffee';
import PasswordIcon from '@material-ui/icons/LockOpenTwoTone';
import {
  Grid,
  Paper,
  Button,
  TextField,
  Typography,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@material-ui/core';
import { useHistory, useLocation } from 'react-router-dom';
import BackButton from '../../../../../backButton';
import { useMutation, gql } from '@apollo/client';
const RESET_PASSWORD = gql`
  mutation ResetPassword($reset: ResetPasswordInput) {
    resetPassword(reset: $reset) {
      name
      status
      token
      _id
      address {
        street
        city
        state
        zipcode
        country
      }
      contactInfo {
        firstName
        lastName
        middleName
        businessName
        email
        phone
      }
      credentals {
        email
        name
      }
      requestStatus {
        code
        message
        success
      }
    }
  }
`;

const AuthenticateNewPassword = (props) => {
  const [user, setUser] = useGlobal(USER);
  const location = useLocation();
  const classes = useStyles();
  const [OTP, setOTP] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [repeatPassword, setRepeatPassword] = useState('');
  const [dialogOpen, setDialogOpen] = useState(false);
  const [dialogType, setDialogType] = useState();
  const history = useHistory();
  const [resetPassword, { loading }] = useMutation(RESET_PASSWORD, {
    onCompleted(data) {
      // setShowProgress(false);
      const { resetPassword } = data;
      const { requestStatus } = resetPassword;
      if (requestStatus.success === true) {
        delete data.resetPassword.requestStatus;
        setDialogOpen(true);
        setDialogType(requestStatus.code);
        setUser(resetPassword);
        history.push('/user/welcome');
      } else {
        console.log(data);
        setDialogOpen(true);
        setDialogType(requestStatus.code);
      }
    },
    onError: (err) => {
      //  setShowProgress(false);
      console.log(err);
      debugger;
    },
  });
  const onOTPChange = (event) => {
    const { value } = event.target;
    setOTP(value);
  };
  const onPasswordChange = (event) => {
    setPassword(event.target.value);
  };
  const onRepeatPasswordChange = (event) => {
    setRepeatPassword(event.target.value);
  };
  const onDialogClick = () => {
    setDialogOpen(false);
    switch (dialogType) {
      case '200':
        debugger;
        //   history.push('/coffees');
        break;
      case 'error':
      default:
        break;
    }
  };
  const onClick = () => {
    resetPassword({ variables: { reset: { OTP, email, password } } });
  };
  const testOTP = () => {
    return OTP.length === 12;
  };
  const testPassword = () => {
    //return password.length>2
    return /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])([a-zA-Z0-9]{8,})$/.test(
      password
    );
  };
  const testRepeatPassword = () => {
    return repeatPassword.length > 0 && password === repeatPassword;
  };
  const onDlogButtonClick = () => {
    setDialogOpen(false);
  };
  const renderDialog = () => {
    let title = '';
    let message = '';
    let buttonText = '';

    switch (dialogType) {
      case '200':
        title = 'success';
        message = 'good';
        buttonText = 'thank you';
        break;

      default:
        title = 'error';
        message = 'there was a problem';
        buttonText = 'try again';
        break;
    }

    return dialogOpen ? (
      <Dialog
        open={dialogOpen}
        aria-labelledby="alert-dialog-title"
        aria-describedby="join-dialog-description"
      >
        <DialogTitle id="join-dialog-title">{title}</DialogTitle>
        <DialogContent>{message}</DialogContent>
        <DialogActions>
          <Button onClick={onDlogButtonClick} color="primary">
            {buttonText}
          </Button>
        </DialogActions>
      </Dialog>
    ) : null;
  };
  const renderInputs = () => {
    return (
      <Paper elevation={2} className={classes.inputs}>
        <TextField
          required
          error={!testOTP()}
          helperText="enter a valid OTP"
          value={OTP}
          type="password"
          label="OTP"
          className={classes.textField}
          onChange={onOTPChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <OTPIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          value={password}
          label="Password"
          error={!testPassword()}
          helperText="at least 8 characters, 1 number, 1 upper and 1 lowercase"
          required
          className={classes.textField}
          type="password"
          autoComplete="new-password"
          margin="normal"
          onChange={onPasswordChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <PasswordIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          value={repeatPassword}
          label="Repeat Password"
          error={!testRepeatPassword()}
          helperText="passwords must match"
          required
          className={classes.textField}
          type="password"
          margin="normal"
          onChange={onRepeatPasswordChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <PasswordIcon />
              </InputAdornment>
            ),
          }}
        />
      </Paper>
    );
  };

  useEffect(() => {
    const urlSearch = new URLSearchParams(location.search);
    const email = urlSearch.get('email');
    setEmail(email);
  }, [location]);
  return (
    <div className={classes.page}>
      {renderDialog()}
      <Grid container className={classes.header}>
        <BackButton />
      </Grid>
      <Grid
        container
        direction="column"
        spacing={0}
        wrap="nowrap"
        className={classes.content}
      >
        <Paper elevation={2} className={classes.inputs}>
          <Typography variant="h4">Password assistance</Typography>
          <Typography paragraph>
            Enter the OTP address associated with your Purfect Blend account.
          </Typography>
          {renderInputs()}
          <Grid container className={classes.buttons}>
            <Button
              variant="outlined"
              style={{ marginRight: 16 }}
              onClick={() => history.goBack()}
              color="primary"
            >
              cancel
            </Button>
            <Button
              variant="outlined"
              disabled={!testOTP()}
              onClick={onClick}
              color="primary"
            >
              go
            </Button>
          </Grid>
        </Paper>
      </Grid>
    </div>
  );
};
export default AuthenticateNewPassword;
