import React, {useGlobal,} from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {useHistory} from "react-router-dom";
import BackButton from '../../../backButton';
import {MOUTHFEEL,ACIDITY,CARAMEL,FRUIT,VARIETAL,} from '../../../../constant/learning';
import sliderValues from './steps';
import Quality from '../quality';
import text from '../text';
const Fruit=(props)=>{
	const classes = useStyles(),
	history=useHistory(),
	[varietal]= useGlobal(VARIETAL),
	[mouthFeel]= useGlobal(MOUTHFEEL),
	[acidity, ]= useGlobal(ACIDITY),
	[caramel,]=useGlobal(CARAMEL),
	[fruit,setFruit]=useGlobal(FRUIT),
	onClick=(fruit)=>{
		history.push('/learning/flower')
		setFruit(fruit);
	};
	if(!varietal || !mouthFeel || !acidity || !caramel){
		history.push('/learning')
		return null;
	};
	return (
		<div className={classes.page}>
			<Grid container className={classes.header}>
				<BackButton />
			</Grid>
			<Typography 
				variant='h5' 
				gutterBottom
				align='center'>
					{varietal&&`${varietal.region}-${varietal.roast}`}
			</Typography>
			<Quality
				value={fruit}
				title='🚫 Fruit 🍋'
				text={text.fruit}
				values={sliderValues}
				onClick={onClick}/>
		</div>)
};
export default Fruit;
