import React, { useGlobal, useState } from 'reactn';
import useStyles from './styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import Purchase from '../../../customBlend/purchase';
import { BLEND_SHOPPING_CART, COFFEES } from '../../../../../constant/coffee';
import decafIcon from '../../../../../images/decaf.png';
import notDecafIcon from '../../../../../images/notDecaf.png';
const Items = (props) => {
  const classes = useStyles();
  const [coffees] = useGlobal(COFFEES);
  const [deletePurchaseId, setDeletePurchaseId] = useState('');
  const [purchases, setPurchases] = useGlobal(BLEND_SHOPPING_CART);
  const [dialogOpen, setDialogOpen] = useState(false);
  const getCoffee = (key) => {
    const k = parseInt(key);
    return coffees.find((c) => parseInt(c.key) === k);
  };
  const findPurchases = (blend, purchases) => {
    const result = purchases.filter((purchase) => {
      return blend.blendId === purchase.blendId;
    });
    return result;
  };
  const onPurchaseChange = (event) => {
    const { purchaseId, price, quantity } = event;
    const _purchases = [...purchases];
    const purchase = _purchases.find(
      (purchase) => purchase.purchaseId === purchaseId
    );
    if (purchase) {
      purchase.quantity = quantity;
      purchase.price = price;
    } else {
      debugger;
    }
    setPurchases(_purchases);
  };
  const onDelete = () => {
    const filtered = purchases.filter(
      (purchase) => purchase.purchaseId != deletePurchaseId
    );
    setPurchases(filtered);
    setDialogOpen(false);
  };
  const onDeleteClick = (purchaseId) => {
    setDeletePurchaseId(purchaseId);
    setDialogOpen(true);
  };
  const renderPurchase = (purchase, i) => {
    return (
      <Purchase
        key={i}
        purchaseId={purchase.purchaseId}
        quantity={purchase.quantity}
        onDelete={onDeleteClick}
        price={purchase.price}
        onPurchaseChange={onPurchaseChange}
      />
    );
  };
  const renderPurchases = (blendPurchases, blend) => {
    const purchases = findPurchases(blend, blendPurchases);
    return (
      <div className={classes.purchases}>
        {purchases.map((purchase, i) => renderPurchase(purchase, i))}
      </div>
    );
  };
  const renderDescription = (purchase) => {
    const { coffees, i } = purchase;
    const coffeeComponents = coffees.map((b, j) => {
      const coffee = getCoffee(b.coffeeId);
      const percentage = b.percentage;
      const { region, roast } = coffee;
      return (
        <div key={j} className={classes.desc}>
          <Typography key={j} className={classes.description}>
            {`${region} ${roast} ${percentage}%`}
          </Typography>
          <img
            className={classes.decafIcon}
            src={coffee.decaf == true ? decafIcon : notDecafIcon}
          />
        </div>
      );
    });
    return <div className={classes.descriptions}>{coffeeComponents}</div>;
  };
  const renderItems = () => {
    const blendPurchases = purchases.filter((p) => p.quantity > 0);
    const blends = blendPurchases.reduce((acc, purchase) => {
      if (acc.find((p) => p.blendId === purchase.blendId) === undefined) {
        acc.push(purchase);
      }
      return acc;
    }, []);
    return blends.map((blendPurchase, i) => {
      return (
        <Paper elevation={4} className={classes.item} key={Math.random()}>
          {renderDescription(blendPurchase)}
          {renderPurchases(blendPurchases, blendPurchase)}
        </Paper>
      );
    });
  };
  const renderDialog = () => {
    return (
      <Dialog
        open={dialogOpen}
        onClose={() => setDialogOpen(false)}
        aria-labelledby="alert-dialog-title"
      >
        <DialogTitle id="remove-dialog-title">
          Remove this purchase from your cart?
        </DialogTitle>
        <DialogActions>
          <Button onClick={() => setDialogOpen(false)} color="primary">
            Cancel
          </Button>
          <Button onClick={onDelete} color="primary">
            Yes
          </Button>
        </DialogActions>
      </Dialog>
    );
  };
  return purchases && purchases.length > 0 ? (
    <Paper elevation={4} className={classes.content}>
      {renderDialog()}
      <Typography className={classes.title}>Custom Blends</Typography>
      {renderItems()}
    </Paper>
  ) : null;
};
export default Items;
