import React, { useGlobal, useState } from 'reactn';
import useStyles from './styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import Purchase from '../../../coffees/purchase';
import { COFFEES_SHOPPING_CART, COFFEES } from '../../../../../constant/coffee';
import decafIcon from '../../../../../images/decaf.png';
import notDecafIcon from '../../../../../images/notDecaf.png';

//import text from '../text';
const Items = (props) => {
  const classes = useStyles();
  const [coffees] = useGlobal(COFFEES);
  const [deletePurchaseId, setDeletePurchaseId] = useState('');
  const [purchases, setPurchases] = useGlobal(COFFEES_SHOPPING_CART);
  const [dialogOpen, setDialogOpen] = useState(false);
  const onPurchaseChange = (event) => {
    const { coffee, purchaseId, price, quantity } = event;
    const _purchases = [...purchases];
    const purchase = _purchases.find(
      (purchase) => purchase.purchaseId === purchaseId
    );
    if (purchase) {
      purchase.quantity = quantity;
      purchase.price = price;
    } else {
      _purchases.push({
        coffee,
        quantity,
        price,
        purchaseId,
        quantity,
      });
    }
    setPurchases(_purchases);
  };

  const onDelete = () => {
    const filtered = purchases.filter(
      (purchase) => purchase.purchaseId != deletePurchaseId
    );
    setPurchases(filtered);
    setDialogOpen(false);
  };
  const onDeleteClick = (purchaseId) => {
    setDeletePurchaseId(purchaseId);
    setDialogOpen(true);
  };
  const renderPurchase = (purchase, coffee, i) => {
    return (
      <Purchase
        key={i}
        purchaseId={purchase.purchaseId}
        quantity={purchase.quantity}
        onDelete={onDeleteClick}
        price={purchase.price}
        onPurchaseChange={onPurchaseChange}
        coffee={coffee}
      />
    );
  };
  const findCoffee = (key) => {
    return coffees.find((c) => c.key === key);
  };
  const findPurchases = (coffee, purchases) => {
    const result = purchases.filter((purchase) => {
      return coffee === purchase.coffee;
    });
    return result;
  };
  const renderItems = () => {
    const coffeePurchases = purchases.filter((p) => p.quantity > 0);
    const coffees = coffeePurchases.reduce((acc, purchase) => {
      if (acc.find((p) => p.coffee.key === purchase.coffee.key) === undefined) {
        acc.push(purchase);
      }
      return acc;
    }, []);
    return coffees.map((coffeePurchase, i) => {
      const { coffee } = coffeePurchase;
      return (
        <Paper elevation={4} className={classes.item} key={Math.random()}>
          <div className={classes.desc}>
            <Typography className={classes.coffeeTitle}>
              {coffee.region} - {coffee.roast}{' '}
            </Typography>
            <img
              className={classes.decafIcon}
              src={coffee.decaf === true ? decafIcon : notDecafIcon}
            />
          </div>
          {renderPurchases(coffeePurchases, coffee)}
        </Paper>
      );
    });
  };
  const renderPurchases = (coffeePurchases, coffee) => {
    const purchases = findPurchases(coffee, coffeePurchases);

    return (
      <div className={classes.purchases}>
        {purchases.map((purchase, i) => renderPurchase(purchase, coffee, i))}
      </div>
    );
  };
  const renderDialog = () => {
    return (
      <Dialog
        open={dialogOpen}
        onClose={() => setDialogOpen(false)}
        aria-labelledby="alert-dialog-title"
      >
        <DialogTitle id="remove-dialog-title">
          Remove this item from your cart?
        </DialogTitle>
        <DialogActions>
          <Button onClick={() => setDialogOpen(false)} color="primary">
            Cancel
          </Button>
          <Button onClick={onDelete} color="primary">
            Yes
          </Button>
        </DialogActions>
      </Dialog>
    );
  };
  return purchases && purchases.length > 0 ? (
    <Paper elevation={4} className={classes.content}>
      {renderDialog()}
      <Typography className={classes.title}>Coffees</Typography>
      {renderItems()}
    </Paper>
  ) : null;
};
export default Items;
