import React, { useState, useEffect } from 'reactn';
import EmailIcon from '@material-ui/icons/EmailTwoTone';
import useStyles from './styles';
import { useHistory } from 'react-router-dom';
import BackButton from '../../../../backButton';
import {
  InputAdornment,
  Button,
  Paper,
  Grid,
  TextField,
  Typography,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  DialogContentText,
} from '@material-ui/core';
import { useLazyQuery, gql } from '@apollo/client';
const FORGOTPASSWORD = gql`
  query forgotPassword($user: UserVerification) {
    forgotPassword(user: $user) {
      message
      code
      success
    }
  }
`;
const ForgotPassword = (props) => {
  const [success, setSuccess] = useState(false);
  const classes = useStyles();
  const [email, setEmail] = useState('');
  const [dialogOpen, setDialogOpen] = useState(false);
  const [dialogMessage, setDialogMessage] = useState();
  const [type, setType] = useState();
  const [OTPValue, setOTPValve] = useState('');
  const [doFogotPassword, { loading, error, data }] =
    useLazyQuery(FORGOTPASSWORD);
  const history = useHistory();
  const onEmailChange = (event) => {
    setEmail(event.target.value.toLocaleLowerCase());
  };
  const onDialogClick = (otp) => {
    if (success) history.goBack();
  };
  const onClick = () => {
    const lowerCaseEmail = email.toLocaleLowerCase();
    doFogotPassword({ variables: { user: { email: lowerCaseEmail } } });
  };
  const testEmail = () => {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  };
  const renderDialog = () => {
    return dialogOpen ? (
      <Dialog
        open={dialogOpen}
        aria-labelledby="alert-dialog-title"
        aria-describedby="join-dialog-description"
      >
        <DialogTitle id="join-dialog-title">
          {'Purfect Blend password assist'}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="join-dialog-description">
            {dialogMessage}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={onDialogClick} color="primary">
            {'Please continue'}
          </Button>
        </DialogActions>
      </Dialog>
    ) : null;
  };
  useEffect(() => {
    if (data) {
      const { code, success, message } = data.forgotPassword;
      setSuccess(success);
      setDialogOpen(true);
      setType(code);
      if (success && code === '462')
        setDialogMessage(
          `For your security, we need to authenticate your request. We've sent a One Time Password (OTP) to the your email. Please respond to the link`
        );
    }
  }, [data]);
  useEffect(() => {
    if (error) debugger;
  }, [error]);

  return (
    <div className={classes.page}>
      {renderDialog()}
      <Grid container className={classes.header}>
        <BackButton />
      </Grid>
      <Grid
        container
        direction="column"
        spacing={0}
        wrap="nowrap"
        className={classes.content}
      >
        <Paper elevation={2} className={classes.inputs}>
          <Typography variant="h4">Password assistance</Typography>
          <Typography paragraph>
            Enter the email address associated with your Purfect Blend account.
          </Typography>
          <TextField
            required
            error={!testEmail()}
            helperText="enter a valid email"
            value={email}
            type="email"
            label="email"
            className={classes.textField}
            onChange={onEmailChange}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <EmailIcon />
                </InputAdornment>
              ),
            }}
          />
          <Grid container className={classes.buttons}>
            <Button
              variant="outlined"
              style={{ marginRight: 16 }}
              onClick={() => history.goBack()}
              color="primary"
            >
              cancel
            </Button>
            <Button
              variant="outlined"
              disabled={!testEmail()}
              onClick={onClick}
              color="primary"
            >
              go
            </Button>
          </Grid>
        </Paper>
      </Grid>
    </div>
  );
};
export default ForgotPassword;
