import React, { useGlobal } from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import { LAST_COFFEE_ORDER } from '../../../../../constant/coffee';
import text from '../../text';
const Address = () => {
  const classes = useStyles();
  const [lastOrder] = useGlobal(LAST_COFFEE_ORDER);
  const renderAddress = () => {
    const items = Object.entries(lastOrder.shippingAddress)
      .filter((i) => i[0] !== '__typename')
      .filter((i) => i[1] !== null && i[1].length > 0)
      .map((entity, i) => (
        <Grid container className={classes.addressItemGrid} key={i}>
          <Typography variant="subtitle1" className={classes.itemTitle}>
            {entity[0]}
          </Typography>
          <Typography variant="subtitle1" className={classes.item}>
            {entity[1]}
          </Typography>
        </Grid>
      ));
    const leftColumn = [],
      rightColumn = [];
    items.forEach((item, i) => {
      if (i % 2 === 0) leftColumn.push(item);
      else rightColumn.push(item);
    });
    return (
      <Grid className={classes.addressItems}>
        <Grid className={classes.leftColumn}>{leftColumn}</Grid>
        <Grid className={classes.rightColumn}>{rightColumn}</Grid>
      </Grid>
    );
  };

  return (
    <Paper elevation={3} className={classes.content}>
      <Typography className={classes.title}>
        {text.thankyou.address.title}
      </Typography>
      {renderAddress()}
    </Paper>
  );
};
export default Address;
