import React, { useGlobal, useEffect } from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import { useHistory } from 'react-router-dom';
import BackButton from '../../../backButton';
import UserButtons from '../../user/buttons';
import Sidebar from '../../../sidebar';
import Copyright from '../../../copyright';
import ShoppingCartButton from '../../buy/shoppingCart/button';
import { MOUTHFEEL, ACIDITY } from '../../../../constant/coffee';
import sliderValues from './steps';
import Quality from '../quality';
import text from '../text';
import HeaderImg from '../../../headerImg';
import MenuBar from '../../../menuBar';
const Acidity = (props) => {
  const classes = useStyles();
  const [mouthFeel] = useGlobal(MOUTHFEEL);
  const [acidity, setAcidity] = useGlobal(ACIDITY);
  const history = useHistory();
  const onClick = (acidity) => {
    history.push('/customblend/caramel');
    setAcidity(acidity);
  };
  useEffect(() => {
    if (!mouthFeel) {
      history.push('/customblend');
    }
  }, [mouthFeel]);

  return (
    <div className={classes.page}>
      <Grid container className={classes.header}>
        <div>
          <Sidebar />
        </div>
        <ShoppingCartButton />
        <UserButtons />
      </Grid>
      <div className={classes.menubar}>
        <BackButton />
        <MenuBar dept="custom" />
      </div>
      <HeaderImg size="small" />
      <Quality
        value={acidity}
        title={text.acidity.title}
        text={text.acidity}
        values={sliderValues}
        onClick={onClick}
      />
      <Copyright />
    </div>
  );
};
export default Acidity;
