import React, { useGlobal, useState } from 'reactn';
import useStyles from './styles';
import Sidebar from '../../sidebar';
import BackButton from '../../backButton';
import HeaderImage from '../../headerImg';
import UserButtons from '../user/buttons';
import ShareButton from '../share/button';
import ShoppingCartButton from '../buy/shoppingCart/button';
import MorePricesIcon from '@material-ui/icons/AddCircleOutlineOutlined';
import Purchase from './purchase';
import Copyright from '../../copyright';
import { useHistory } from 'react-router-dom';
import lightImage from '../customBlend/images/blends/light.png';
import mediumImage from '../customBlend/images/blends/medium.png';
import frenchImage from '../customBlend/images/blends/french.png';
import viennaImage from '../customBlend/images/blends/vienna.png';
import ultraLightImage from '../customBlend/images/blends/ultraLight.png';
import {
  Grid,
  Paper,
  Typography,
  IconButton,
  TextField,
  MenuItem,
} from '@material-ui/core';
import decafIcon from '../../../images/decaf.png';
import notDecafIcon from '../../../images/notDecaf.png';
import MenuBar from '../../menuBar';
import {
  MOUTHFEEL,
  ACIDITY,
  CARAMEL,
  FRUIT,
  FLOWER,
  COFFEES,
  COFFEES_SHOPPING_CART,
} from '../../../constant/coffee';
const decafSelect = [
  {
    value: 'all',
    label: 'All',
  },
  {
    value: 'decaf',
    label: 'decafe',
  },
  {
    value: 'regular',
    label: 'regular',
  },
];
const Coffees = () => {
  const classes = useStyles();
  const history = useHistory();
  const [coffees] = useGlobal(COFFEES);
  const [purchases, setPurchases] = useGlobal(COFFEES_SHOPPING_CART);
  const [decafFilter, setDecafFilter] = useState('all');
  const renderCoffees = () => {
    return coffees && purchases
      ? filter(coffees).map((coffee, i) => renderCoffee(coffee, i))
      : null;
  };
  const onPurchaseChange = (event) => {
    const { coffee, purchaseId, price, quantity } = event;
    const _purchases = [...purchases];
    const purchase = _purchases.find(
      (purchase) => purchase.purchaseId === purchaseId
    );
    if (purchase) {
      purchase.quantity = quantity;
      purchase.price = price;
    } else {
      _purchases.push({
        coffee,
        quantity,
        price,
        purchaseId,
      });
    }
    setPurchases(_purchases);
  };
  const splitKey = (key) => {
    const splitKey = key.split('-');
    return splitKey;
  };
  const getCoffeeId = (key) => {
    return splitKey(key)[0];
  };
  const filter = () => {
    if (decafFilter === 'all') return coffees;
    if (decafFilter === 'decaf')
      return coffees.filter((coffee) => coffee.decaf === true);
    return coffees.filter((coffee) => coffee.decaf === false);
  };
  const onDeleteClick = (purchaseId) => {
    const filtered = purchases.filter(
      (purchase) => purchase.purchaseId !== purchaseId
    );
    setPurchases(filtered);
  };
  const onAddClick = (coffee) => {
    if (purchases.find((p) => p.quantity === 0)) return;
    const { key } = coffee;
    const count = purchases.filter((p) => {
      return p.coffee.key === key;
    }).length;
    const purchaseId = `${key}-${count}`;
    purchases.push({
      coffee,
      quantity: 0,
      price: coffee.prices[0],
      purchaseId,
    });
    setPurchases(purchases);
  };
  const handleCurrentCoffee = (c) => {
    history.push({
      pathname: '/coffee',
      search: `?coffee=${c._id}`,
    });
  };
  const renderPurchases = (coffee) => {
    const _myPurchases = purchases.filter(
      (purchase) => coffee.key === getCoffeeId(purchase.purchaseId)
    );
    return (
      <div elevation={3} className={classes.purchases}>
        {_myPurchases.length ? (
          _myPurchases.map((purchase, i) => {
            return (
              <Purchase
                key={i}
                purchaseId={purchase.purchaseId}
                quantity={purchase.quantity}
                onDelete={onDeleteClick}
                price={purchase.price}
                onPurchaseChange={onPurchaseChange}
                coffee={coffee}
              />
            );
          })
        ) : (
          <Purchase
            key={Math.random()}
            purchaseId={`${coffee.key}-${0}`}
            quantity={0}
            price={coffee.prices[0]}
            onDelete={onDeleteClick}
            onPurchaseChange={onPurchaseChange}
            coffee={coffee}
          />
        )}
        <IconButton
          className={classes.addButton}
          size="small"
          color="primary"
          onClick={() => onAddClick(coffee)}
        >
          <MorePricesIcon />
        </IconButton>
      </div>
    );
  };
  const renderDecafBadge = (coffee) => {
    const { decaf } = coffee;
    return (
      <span className={classes.decafContainer}>
        {decaf === true ? (
          <Typography className={classes.decafText}>decaf</Typography>
        ) : null}
        <img
          className={classes.decafIcon}
          src={decaf === true ? decafIcon : notDecafIcon}
          alt="decaf"
          aria-label="decaf"
        />
      </span>
    );
  };
  const handleDecafSelect = (event) => {
    const { value } = event.target;
    setDecafFilter(value);
  };
  const renderDecafSelect = () => {
    return (
      <TextField
        className={classes.decafSelect}
        select={true}
        label="filter"
        value={decafFilter}
        onChange={handleDecafSelect}
      >
        {decafSelect.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  };
  const renderCoffee = (coffee, i) => {
    let background = null;
    switch (coffee.roast) {
      case 'light':
        background = lightImage;
        break;
      case 'medium':
        background = mediumImage;
        break;
      case 'french':
        background = frenchImage;
        break;
      case 'vienna':
        background = viennaImage;
        break;

      default:
        background = ultraLightImage;
        break;
    }
    return (
      <Paper elevation={2} key={i} className={classes.coffee}>
        <Paper
          className={classes.properties}
          style={{ backgroundImage: `url(${background})` }}
          onClick={() => handleCurrentCoffee(coffee)}
        >
          <div className={classes.propertyContent}>
            <Grid className={classes.coffeeHeader} elevation={3}>
              <Typography className={classes.coffeeTitle}>
                {coffee.region} - {coffee.roast}
              </Typography>
              {renderDecafBadge(coffee)}
            </Grid>
            <Typography className={classes.coffeeParagraph}>
              {coffee.paragraphs}
            </Typography>
            <Typography className={classes.coffeeFlavors}>
              <span className={classes.h2}>flavors: </span>
              {renderFlavors(coffee.flavors)}
            </Typography>
            <Typography className={classes.coffeeFlavors}>
              <span className={classes.h2}>qualities: </span>
              {renderQualities(coffee.qualities)}
            </Typography>
            {renderProfile(coffee)}
          </div>
        </Paper>
        {renderPurchases(coffee)}
      </Paper>
    );
  };
  const renderProfile = (coffee) => {
    return (
      <div className={classes.coffeeProfile}>
        {MOUTHFEEL}:{coffee[MOUTHFEEL]},&nbsp;&nbsp;{ACIDITY}:{coffee[ACIDITY]}
        ,&nbsp;&nbsp;
        {CARAMEL}:{coffee[CARAMEL]},&nbsp;&nbsp;{FRUIT}:{coffee[FRUIT]}
        ,&nbsp;&nbsp;{FLOWER}:{coffee[FLOWER]}
      </div>
    );
  };
  const renderFlavors = (flavors) => {
    return flavors.map((f, i) => `${f},   `);
  };
  const renderQualities = (qualities) => {
    return qualities.map((f, i) => `${f},  `);
  };
  return coffees && purchases ? (
    <div className={classes.page}>
      <Grid container className={classes.header}>
        <div>
          <Sidebar />
        </div>
        <ShoppingCartButton dept="coffees" />
        <UserButtons />
      </Grid>
      <div className={classes.menubar}>
        <BackButton gotTo="/coffee" />
        <MenuBar dept="coffees" />
      </div>

      <HeaderImage size="small" />
      <ShareButton baseStyle={classes.ShareButton} link="coffees" />
      <div className={classes.head}>
        <Typography className={classes.heading}>Our coffees</Typography>
        {renderDecafSelect()}
      </div>
      {renderCoffees()}
      <Copyright />
    </div>
  ) : null;
};

export default Coffees;
