import React, { useGlobal, useState } from 'reactn';
import useStyles from './styles';
import { useHistory } from 'react-router-dom';
import Sidebar from '../../sidebar';
import BackButton from '../../backButton';
import HeaderImage from '../../headerImg';
import ShareButton from '../share/button';
import UserButtons from '../user/buttons';
import MorePricesIcon from '@material-ui/icons/AddCircleOutlineOutlined';
import ShoppingCartButton from '../buy/shoppingCart/button';
import {
  Grid,
  Paper,
  Typography,
  IconButton,
  TextField,
  MenuItem,
} from '@material-ui/core';
import MenuBar from '../../menuBar';
import Copyright from '../../copyright';
import decafIcon from '../../../images/decaf.png';
import notDecafIcon from '../../../images/notDecaf.png';
import Purchase from './purchase';
import lightImage from '../customBlend/images/blends/light.png';
import mediumImage from '../customBlend/images/blends/medium.png';
import ultraLightImage from '../customBlend/images/blends/ultraLight.png';
import viennaImage from '../customBlend/images/blends/vienna.png';
import frenchImage from '../customBlend/images/blends/french.png';
import {
  MOUTHFEEL,
  ACIDITY,
  CARAMEL,
  FRUIT,
  FLOWER,
  COFFEES,
  OUR_BLENDS,
  OUR_BLENDS_SHOPPING_CART,
} from '../../../constant/coffee';
const decafSelect = [
  {
    value: 'all',
    label: 'All',
  },
  {
    value: 'decaf',
    label: 'decafe',
  },
  {
    value: 'regular',
    label: 'regular',
  },
];
const OurBlends = () => {
  const classes = useStyles();
  const history = useHistory();
  const [ourBlends] = useGlobal(OUR_BLENDS);
  const [coffees] = useGlobal(COFFEES);
  const [purchases, setPurchases] = useGlobal(OUR_BLENDS_SHOPPING_CART);
  const [decafFilter, setDecafFilter] = useState('all');
  const splitKey = (key) => {
    const splitKey = key.split('-');
    return splitKey;
  };
  const getBlendId = (key) => {
    return splitKey(key)[0];
  };
  const getCoffee = (id) => {
    return coffees.find((c) => c._id === id);
    //  return coffees.find((c) = c._id === id));
  };
  const filter = () => {
    if (decafFilter === 'all') return ourBlends;
    if (decafFilter === 'decaf')
      return ourBlends.filter((b) => b.decaf === true);
    return ourBlends.filter((b) => b.decaf === false);
  };
  const onPurchaseChange = (event) => {
    const { type, ourBlend, purchaseId, price, quantity } = event;
    const _purchases = [...purchases];
    const purchase = _purchases.find(
      (purchase) => purchase.purchaseId === purchaseId
    );
    if (purchase) {
      purchase.quantity = quantity;
      purchase.price = price;
    } else {
      _purchases.push({
        ourBlend,
        quantity,
        price,
        purchaseId,
        quantity,
      });
    }
    setPurchases(_purchases);
  };
  const renderCoffees = (coffeeIds) => {
    return (
      <div className={classes.coffees}>
        <span className={classes.h2}>coffees: </span>

        {coffeeIds.map((_id, i) => {
          const coffee = coffees.find((c) => c._id === _id);
          return (
            <Typography
              key={i}
              className={classes.coffee}
            >{`${coffee.region}-${coffee.roast}`}</Typography>
          );
        })}
      </div>
    );
  };
  const renderOurblends = () => {
    return ourBlends && purchases
      ? filter(ourBlends).map((blend, i) => renderOurblend(blend, i))
      : null;
  };
  const onDeleteClick = (purchaseId) => {
    const filtered = purchases.filter(
      (purchase) => purchase.purchaseId != purchaseId
    );
    setPurchases(filtered);
  };
  const onAddClick = (ourBlend) => {
    if (purchases.find((p) => p.quantity === 0)) return;
    const { _id } = ourBlend;
    const count = purchases.filter((p) => {
      return p.ourBlend._id === _id;
    }).length;
    const purchaseId = `${_id}-${count}`;
    purchases.push({
      ourBlend,
      quantity: 0,
      price: ourBlend.prices[0],
      purchaseId,
    });
    setPurchases(purchases);
  };
  const handleDecafSelect = (event) => {
    const { value } = event.target;
    setDecafFilter(value);
  };
  const renderDecafSelect = () => {
    return (
      <TextField
        className={classes.decafSelect}
        select={true}
        label="filter"
        value={decafFilter}
        onChange={handleDecafSelect}
      >
        {decafSelect.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  };
  const renderPurchases = (ourBlend) => {
    const _myPurchases = purchases.filter(
      (purchase) => ourBlend._id === getBlendId(purchase.purchaseId)
    );
    return (
      <div elevation={3} className={classes.purchases}>
        {_myPurchases.length ? (
          _myPurchases.map((purchase, i) => {
            return (
              <Purchase
                key={i}
                purchaseId={purchase.purchaseId}
                quantity={purchase.quantity}
                onDelete={onDeleteClick}
                price={purchase.price}
                onPurchaseChange={onPurchaseChange}
                ourBlend={ourBlend}
              />
            );
          })
        ) : (
          <Purchase
            key={Math.random()}
            purchaseId={`${ourBlend._id}-${0}`}
            quantity={0}
            price={ourBlend.prices[0]}
            onDelete={onDeleteClick}
            onPurchaseChange={onPurchaseChange}
            ourBlend={ourBlend}
          />
        )}
        <IconButton
          className={classes.addButton}
          size="small"
          color="primary"
          onClick={() => onAddClick(ourBlend)}
        >
          <MorePricesIcon />
        </IconButton>
      </div>
    );
  };
  const handleOurBlend = (c) => {
    console.log(c);
    //	history.push('/')
    history.push({
      pathname: '/ourblend',
      search: `?ourblend=${c._id}`,
    });
  };
  const getImage = (roast) => {
    debugger;
    if (roast === 'ultralight') return ultraLightImage;
    if (roast === 'light') return lightImage;
    if (roast === 'medium') return mediumImage;
    if (roast === 'vienna') return viennaImage;
    if (roast === 'french') return frenchImage;

    // switch (roast) {
    // 	case 'light':
    // 	  background = lightImage;
    // 	  break;
    // 	case 'medium':
    // 	  background = mediumImage;
    // 	  break;
    // 	case 'french':
    // 	  background = frenchImage;
    // 	  break;
    // 	case 'vienna':
    // 	  background = viennaImage;
    // 	  break;

    // 	default:
    // 	  background = ultraLightImage;
    // 	  break;
    //   }
  };
  const getBlendStyle = (coffees) => {
    const roasts = coffees
      .map((c) => getCoffee(c))
      .map((c) => c.roast)
      .sort();
    const length = coffees.length;
    let position = null;
    let repeat = 'no-repeat';
    let size = null;
    switch (length) {
      case 1:
        size = 'inherited';
        position = 'inherited';
        repeat = 'inherited';
        break;
      case 2:
        size = '50% 100%, 50% 100%';
        position = 'top left, top right';
        break;
      case 3:
        size = '33.4% 100%, 33.4% 100%';
        position = 'top left, center, top right';
        break;
      default:
        size = '25.2% 100%, 25.2% 100%, 25.2% 100%, 25.2% 100%';
        position = 'top left, 33.3333%, 66.3333%, bottom right';
        break;
    }
    let images = '';
    roasts.forEach((r, i) => {
      images += `url(${getImage(r)})`;
      if (i < length - 1) {
        debugger;
        images += ',';
      }
    });
    const result = {
      backgroundImage: images,
      backgroundPosition: position,
      backgroundRepeat: repeat,
      backgroundSize: size,
    };
    return result;
  };
  const renderOurblend = (blend, i) => {
    const background = getBlendStyle(blend.coffees);
    debugger;
    return (
      <Paper elevation={2} key={i} className={classes.ourBlend}>
        <Paper
          elevation={2}
          onClick={() => handleOurBlend(blend)}
          style={background}
          className={classes.properties}
        >
          {
            <div className={classes.propertyContent}>
              <Grid className={classes.ourBlendHeader} elevation={3}>
                <Typography className={classes.ourBlendTitle}>
                  {blend.name}
                </Typography>
                {renderDecafBadge(blend)}
              </Grid>
              <Typography className={classes.ourBlendParagraph}>
                {blend.paragraphs}
              </Typography>
              <Typography className={classes.ourBlendFlavors}>
                <span className={classes.h2}>flavors: </span>
                {renderFlavors(blend.flavors)}
              </Typography>
              <Typography className={classes.ourBlendFlavors}>
                <span className={classes.h2}>qualities: </span>
                {renderQualities(blend.qualities)}
              </Typography>
              {renderCoffees(blend.coffees)}
              {renderProfile(blend)}
            </div>
          }
        </Paper>
        {renderPurchases(blend)}
      </Paper>
    );
  };
  const renderProfile = (ourBlend) => {
    return (
      <Grid container className={classes.ourBlendProfile}>
        {MOUTHFEEL}:{ourBlend[MOUTHFEEL]}, {ACIDITY}:{ourBlend[ACIDITY]},{' '}
        {CARAMEL}:{ourBlend[CARAMEL]}, {FRUIT}:{ourBlend[FRUIT]}, {FLOWER}:
        {ourBlend[FLOWER]}
      </Grid>
    );
  };
  const renderFlavors = (flavors) => {
    return flavors.map((f, i) => `${f},  `);
  };
  const renderQualities = (qualities) => {
    return qualities.map((f, i) => `${f},  `);
  };
  const renderDecafBadge = (blend) => {
    const { decaf } = blend;
    return (
      <span className={classes.decafContainer}>
        {decaf === true ? (
          <Typography className={classes.decafText}>decaf</Typography>
        ) : null}
        <img
          className={classes.decafIcon}
          src={decaf === true ? decafIcon : notDecafIcon}
        />
      </span>
    );
  };
  const render = () => {
    return ourBlends && coffees ? (
      <div className={classes.page}>
        <Grid container className={classes.header}>
          <div>
            <Sidebar />
          </div>
          <ShoppingCartButton dept="blends" />
          <UserButtons />
        </Grid>
        <div className={classes.menubar}>
          <BackButton />
          <MenuBar dept="blends" />
        </div>
        <HeaderImage size="small" />
        <ShareButton baseStyle={classes.ShareButton} link="ourblends" />
        <div className={classes.head}>
          <Typography className={classes.heading}>Our Blends</Typography>
          {renderDecafSelect()}
        </div>
        {renderOurblends()}
        <Copyright />
      </div>
    ) : null;
  };
  return render();
};

export default OurBlends;
