import React, { useGlobal, useState, useEffect } from 'reactn';
import useStyles from './styles';
import Purchase from '../purchase';
import Sidebar from '../../../sidebar';
import BackButton from '../../../backButton';
import HeaderImage from '../../../headerImg';
import UserButtons from '../../user/buttons';
import MenuBar from '../../../menuBar';
import ShoppingCartButton from '../../buy/shoppingCart/button';
import Copyright from '../../../copyright';
import {
  Grid,
  Paper,
  Typography,
  IconButton,
  Dialog,
  DialogActions,
  DialogTitle,
  Button,
} from '@material-ui/core';
import decafIcon from '../../../../images/decaf.png';
import notDecafIcon from '../../../../images/notDecaf.png';
import MorePricesIcon from '@material-ui/icons/AddCircleOutlineOutlined';
import ShareButton from '../../share/button';
import { useHistory, useLocation } from 'react-router-dom';
import lightImage from '../../customBlend/images/blends/light.png';
import mediumImage from '../../customBlend/images/blends/medium.png';
import ultraLightImage from '../../customBlend/images/blends/ultraLight.png';
import viennaImage from '../../customBlend/images/blends/vienna.png';
import frenchImage from '../../customBlend/images/blends/french.png';

import {
  MOUTHFEEL,
  ACIDITY,
  CARAMEL,
  FRUIT,
  FLOWER,
  COFFEES,
  OUR_BLENDS_SHOPPING_CART,
  OUR_BLENDS,
} from '../../../../constant/coffee';
const OurBlend = () => {
  const location = useLocation();
  const history = useHistory();
  const classes = useStyles();
  const [ourBlend, setOurBlend] = useState();
  const [purchases, setPurchases] = useGlobal(OUR_BLENDS_SHOPPING_CART);
  const [coffees] = useGlobal(COFFEES);
  const [ourBlends] = useGlobal(OUR_BLENDS);
  const [showErrorDlog, setShowErrorDlog] = useState(false);
  useEffect(() => {
    if (ourBlends) {
      try {
        const id = new URLSearchParams(location.search).get('ourblend');
        const ourBlend = findOurBlend(id);
        if (ourBlend) {
          setOurBlend(ourBlend);
        } else {
          setShowErrorDlog(true);
        }
      } catch (error) {
        setShowErrorDlog(true);
      }
    }
  }, [ourBlend, ourBlends]);

  const findOurBlend = (id) => {
    const ourBlend = ourBlends.find((c) => c._id === id);
    return ourBlend;
  };
  const onPurchaseChange = (event) => {
    const { type, ourBlend, purchaseId, price, quantity } = event;
    const _purchases = [...purchases];
    const purchase = _purchases.find(
      (purchase) => purchase.purchaseId === purchaseId
    );
    if (purchase) {
      purchase.quantity = quantity;
      purchase.price = price;
    } else {
      _purchases.push({
        ourBlend,
        quantity,
        price,
        purchaseId,
      });
    }
    setPurchases(_purchases);
  };
  const splitKey = (key) => {
    const splitKey = key.split('-');
    return splitKey;
  };
  const getBlendId = (key) => {
    return splitKey(key)[0];
  };
  const handleErrorDlogButton = () => {
    setShowErrorDlog(false);
    history.push('/');
  };
  const onDeleteClick = (purchaseId) => {
    const filtered = purchases.filter(
      (purchase) => purchase.purchaseId !== purchaseId
    );
    setPurchases(filtered);
  };
  const onAddClick = (ourBlend) => {
    if (purchases.find((p) => p.quantity === 0)) return;

    const { _id } = ourBlend;
    const count = purchases.filter((p) => {
      return p.ourBlend._id === _id;
    }).length;
    const purchaseId = `${_id}-${count}`;
    purchases.push({
      ourBlend,
      quantity: 0,
      price: ourBlend.prices[0],
      purchaseId,
    });
    setPurchases(purchases);
  };
  const renderErrorDlog = () => {
    if (showErrorDlog == true) {
      return (
        <Dialog
          open={showErrorDlog}
          onClose={() => setShowErrorDlog(false)}
          aria-labelledby="alert-dialog-title"
        >
          <DialogTitle id="remove-dialog-title">
            There was a problem with your request.
          </DialogTitle>
          <DialogActions>
            <Button onClick={handleErrorDlogButton} color="primary">
              ok
            </Button>
          </DialogActions>
        </Dialog>
      );
    }
    return null;
  };
  const renderPurchases = () => {
    const _myPurchases = purchases.filter(
      (purchase) => ourBlend._id === getBlendId(purchase.purchaseId)
    );
    return (
      <div elevation={3} className={classes.purchases}>
        {_myPurchases.length ? (
          _myPurchases.map((purchase, i) => {
            return (
              <Purchase
                key={i}
                purchaseId={purchase.purchaseId}
                quantity={purchase.quantity}
                onDelete={onDeleteClick}
                price={purchase.price}
                onPurchaseChange={onPurchaseChange}
                ourBlend={ourBlend}
              />
            );
          })
        ) : (
          <Purchase
            key={Math.random()}
            purchaseId={`${ourBlend._id}-${0}`}
            quantity={0}
            price={ourBlend.prices[0]}
            onDelete={onDeleteClick}
            onPurchaseChange={onPurchaseChange}
            ourBlend={ourBlend}
          />
        )}
        <IconButton
          className={classes.addButton}
          size="small"
          color="primary"
          onClick={() => onAddClick(ourBlend)}
        >
          <MorePricesIcon />
        </IconButton>
      </div>
    );
  };
  const renderDecafBadge = (ourBlend) => {
    const { decaf } = ourBlend;
    return (
      <span className={classes.decafContainer}>
        {decaf === true ? (
          <Typography className={classes.decafText}>decaf</Typography>
        ) : null}
        <img
          className={classes.decafIcon}
          src={decaf === true ? decafIcon : notDecafIcon}
          alt="decaf"
          aria-label="decaf"
        />
      </span>
    );
  };

  const renderCoffee = (coffee, i) => {
    debugger;

    let background = null;
    switch (coffee.roast) {
      case 'light':
        background = lightImage;
        break;
      case 'medium':
        background = mediumImage;
        break;
      case 'french':
        background = frenchImage;
        break;
      case 'vienna':
        background = viennaImage;
        break;

      default:
        background = ultraLightImage;
        break;
    }
    return (
      <Paper
        key={i}
        className={classes.coffee}
        style={{ backgroundImage: `url(${background})` }}
      >
        <div className={classes.propertyContent}>
          <Typography
            className={classes.coffee}
          >{`${coffee.region}-${coffee.roast}`}</Typography>
          <Typography className={classes.ourBlendFlavors}>
            <span className={classes.h2}>flavors: </span>
            {renderFlavors(coffee.flavors)}
          </Typography>
          <Typography className={classes.ourBlendFlavors}>
            <span className={classes.h2}>qualities: </span>
            {renderQualities(coffee.qualities)}
          </Typography>
          <div>
            {coffee.paragraphs.map((p, i) => (
              <div key={i}>{p}</div>
            ))}
          </div>
          {renderProfile(coffee)}
        </div>
      </Paper>
    );
  };
  const renderCoffees = (coffeeIds) => {
    return (
      <div className={classes.coffees}>
        <span className={classes.h2}>coffees: </span>
        <div className={classes.coffeesContainer}>
          {coffeeIds.map((_id, i) => {
            const coffee = coffees.find((c) => c._id === _id);
            return renderCoffee(coffee, i);
          })}
        </div>
      </div>
    );
  };
  //   const renderCoffee = (coffee, i) => {
  //     return (
  //       <Paper key={i} className={classes.coffee}>
  //         <Typography
  //           className={classes.coffee}
  //         >{`${coffee.region}-${coffee.roast}`}</Typography>
  //         <Typography className={classes.ourBlendFlavors}>
  //           <span className={classes.h2}>flavors: </span>
  //           {renderFlavors(coffee.flavors)}
  //         </Typography>
  //         <Typography className={classes.ourBlendFlavors}>
  //           <span className={classes.h2}>qualities: </span>
  //           {renderQualities(coffee.qualities)}
  //         </Typography>
  //         <div>
  //           {coffee.paragraphs.map((p, i) => (
  //             <div key={i}>{p}</div>
  //           ))}
  //         </div>
  //         {renderProfile(coffee)}
  //       </Paper>
  //     );
  //   };
  const renderOurBlend = () => {
    return (
      <div className={classes.ourBlend}>
        <div elevation={2} className={classes.properties}>
          <Grid className={classes.ourBlendHeader} elevation={3}>
            <Typography className={classes.ourBlendTitle}>
              {ourBlend.name}
            </Typography>
            {renderDecafBadge(ourBlend)}
          </Grid>
          <Typography className={classes.ourBlendParagraph}>
            {ourBlend.paragraphs}
          </Typography>
          <Typography className={classes.ourBlendFlavors}>
            <span className={classes.h2}>flavors: </span>
            {renderFlavors(ourBlend.flavors)}
          </Typography>
          <Typography className={classes.ourBlendFlavors}>
            <span className={classes.h2}>qualities: </span>
            {renderQualities(ourBlend.qualities)}
          </Typography>
          {renderProfile(ourBlend)}

          {renderCoffees(ourBlend.coffees)}
        </div>
        {renderPurchases(ourBlend)}
      </div>
    );
  };
  const renderProfile = (ourBlend) => {
    return (
      <Grid container className={classes.ourBlendProfile}>
        {MOUTHFEEL}:{ourBlend[MOUTHFEEL]}, {ACIDITY}:{ourBlend[ACIDITY]},{' '}
        {CARAMEL}:{ourBlend[CARAMEL]}, {FRUIT}:{ourBlend[FRUIT]}, {FLOWER}:
        {ourBlend[FLOWER]}
      </Grid>
    );
  };
  const renderFlavors = (flavors) => {
    return flavors.map((f, i) => `${f},   `);
  };
  const renderQualities = (qualities) => {
    return qualities.map((f, i) => `${f},  `);
  };
  const render = () => {
    return (
      <div>
        {renderErrorDlog()}

        {ourBlend && coffees ? (
          <div className={classes.page}>
            <Grid container className={classes.header}>
              <div>
                <Sidebar />
              </div>
              <ShoppingCartButton dept="ourBlends" />
              <UserButtons />
            </Grid>
            <div className={classes.menubar}>
              <BackButton />
              <MenuBar />
            </div>
            <HeaderImage size="small" />
            <ShareButton
              baseStyle={classes.ShareButton}
              link={`ourblend?ourblend=${ourBlend._id}`}
            />
            {renderOurBlend()}
            <Copyright />
          </div>
        ) : null}
      </div>
    );
  };
  return render();
};

export default OurBlend;
