import React, { useState } from 'reactn';
import Fab from '@material-ui/core/Fab';
import Grid from '@material-ui/core/Grid';
import {
  TextField,
  Button,
  Paper,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@material-ui/core';
import TwitterIcon from '@material-ui/icons/Twitter';
import ShareIcon from '@material-ui/icons/Share';
import EmailIcon from '@material-ui/icons/Email';
import MessageIcon from '@material-ui/icons/MessageOutlined';
import classNames from 'classnames';
import FacebookButton from '../../../facebook/button';
import useStyles from './styles';
import SendEmail from '../email';
const ShareButton = (props) => {
  const { baseStyle, style, link } = props;
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [showDlog, setShowDlog] = useState(false);
  const [showEmail, setShowEmail] = useState(false);
  const onClick = () => {
      setOpen(!open);
    },
    onShareClick = (share) => {
      setOpen(false);
    };
  const handleShowEmail = () => {
    setOpen(false);
    setShowEmail(true);
    setShowDlog(true);
  };
  const handleCloseDlog = () => {
    setShowDlog(false);
    setShowEmail(false);
  };
  const renderDlog = () => {
    return showDlog ? (
      <Dialog
        open={showDlog}
        aria-labelledby="contact me"
        aria-describedby="test"
      >
        <DialogTitle>{showEmail ? 'Email from' : 'nothing'}</DialogTitle>
        <div>
          {showEmail ? <SendEmail close={handleCloseDlog} link={link} /> : null}
        </div>
      </Dialog>
    ) : null;
  };
  const renderChildren = () => {
    return open ? (
      <Grid container className={classes.children} direction="column">
        <Fab
          className={classes.fab}
          size="small"
          aria-label="twitter"
          onClick={() => onShareClick('twitter')}
        >
          <TwitterIcon className={classes.twitterIcon} />
        </Fab>
        <FacebookButton
          onClick={() => {
            setOpen(false);
          }}
        />
        <Fab
          className={classes.fab}
          aria-label="instagram"
          size="small"
          onClick={() => onShareClick('instagram')}
        >
          <img
            className={classes.instagramIcon}
            src="/svg/share/instagram.png"
          />
        </Fab>
        <Fab
          className={classes.fab}
          aria-label="email"
          size="small"
          onClick={handleShowEmail}
        >
          <EmailIcon color="primary" className={classes.x} />
        </Fab>
        <Fab
          className={classes.fab}
          aria-label="msg"
          size="small"
          onClick={() => onShareClick('msg')}
        >
          <MessageIcon color="primary" className={classes.x} />
        </Fab>
      </Grid>
    ) : null;
  };
  return (
    <div>
      {renderDlog()}
      <Grid
        container
        className={baseStyle ? baseStyle : classes.root}
        direction="column"
        style={style}
      >
        <Fab
          color="primary"
          className={classes.fab}
          aria-label="share"
          size="small"
          onClick={onClick}
        >
          <ShareIcon color="secondary" size="medium" />
        </Fab>
        {renderChildren()}
      </Grid>
    </div>
  );
};
export default ShareButton;
