import React, { useState, useGlobal, useEffect } from 'reactn';
import useStyles from './styles';
import { CUSTOM_SETTINGS } from '../../../../constant/coffee';
import { Paper, TextField, MenuItem } from '@material-ui/core';
const quantities = [
  {
    value: 0,
    display: 'select',
  },
  {
    value: 1,
    display: '1',
  },
  {
    value: 2,
    display: '2',
  },
  {
    value: 3,
    display: '3',
  },
  {
    value: 4,
    display: '4',
  },
  {
    value: 5,
    display: '5',
  },
  {
    value: 6,
    display: '6',
  },
  {
    value: 7,
    display: '7',
  },
  {
    value: 8,
    display: '8',
  },
  {
    value: 9,
    display: '9',
  },
  {
    value: 10,
    display: '10',
  },
  {
    value: 11,
    display: '11',
  },
  {
    value: 12,
    display: '12',
  },
];
const Purchase = (props) => {
  const { onPurchaseChange, purchaseId, onDelete } = props;
  const classes = useStyles();
  const settings = useGlobal(CUSTOM_SETTINGS);
  const [customPrices, setCustomPrices] = useState();
  const [price, setPrice] = useState(
    props.price ? props.price : props.customPrices[0]
  );
  const [quantity, setQuantity] = useState(props.quantity ? props.quantity : 0);
  const generatePriceKey = (p) => {
    const { price, measurement, quantity } = p;
    return `${price}-${measurement}-${quantity}`;
  };
  const onDeleteClick = () => {
    onDelete(purchaseId);
  };
  const onQtyChange = (event) => {
    let q = parseInt(event.target.value);
    if (q < 0) q = 0;
    setQuantity(q);
    onPurchaseChange({ purchaseId, type: 'qty', price, quantity: q });
  };
  const onPriceChange = (event) => {
    const priceKey = event.target.value;
    const price = customPrices.find((price) => {
      return generatePriceKey(price) === priceKey;
    });
    setPrice(price);
    onPurchaseChange({ purchaseId, type: 'price', price, quantity });
  };
  const renderPriceSelect = () => {
    return (
      <TextField
        value={generatePriceKey(price)}
        label="price"
        variant="standard"
        select={true}
        size="small"
        className={classes.price}
        margin="dense"
        onChange={onPriceChange}
      >
        {customPrices.map((price, i) => {
          return (
            <MenuItem
              className={classes.priceMenuItem}
              key={i}
              value={generatePriceKey(price)}
            >
              {`${new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD',
              }).format(price.price)}/${price.quantity} ${price.measurement}`}
            </MenuItem>
          );
        })}
      </TextField>
    );
  };
  const renderQuantitySelect = () => {
    return (
      <TextField
        value={quantity}
        label="quantity"
        variant="standard"
        size="small"
        select={true}
        className={classes.quantity}
        margin="dense"
        onChange={onQtyChange}
      >
        {quantities.map((quantity, i) => {
          return (
            <MenuItem key={i} value={quantity.value}>
              {`${quantity.display}`}
            </MenuItem>
          );
        })}
      </TextField>
    );
  };
  const renderPurchase = () => {
    if (!customPrices || !price) return null;
    return (
      <Paper elevation={3} className={classes.purchaseContent}>
        {renderPriceSelect()}
        {renderQuantitySelect()}
        {quantity ? (
          <div className={classes.purchaseTotal}>
            {`= ${new Intl.NumberFormat('en-US', {
              style: 'currency',
              currency: 'USD',
            }).format(price.price * quantity)}`}
          </div>
        ) : null}
        <span className={classes.deleteEmoji} onClick={() => onDeleteClick()}>
          🗑
        </span>
      </Paper>
    );
  };
  useEffect(() => {
    if (settings) {
      setCustomPrices(settings[0].prices);
    }
  }, [setCustomPrices, settings]);
  return <div className={classes.purchase}>{renderPurchase()}</div>;
};

export default Purchase;
