import BackButton from '../../backButton';
import Sidebar from '../../sidebar';
import HeaderImage from '../../headerImg';
import { useLazyQuery, gql } from '@apollo/client';
import {
  COFFEES,
  USER,
  OUR_BLENDS,
  BLEND_SHOPPING_CART,
  OUR_BLENDS_SHOPPING_CART,
  COFFEES_SHOPPING_CART,
  CUSTOM_SETTINGS,
} from '../.././../constant/coffee';
import { format } from 'date-fns';
import MenuBar from '../../menuBar';
const descendingComparator = (a, b, orderBy) => {
  if (orderBy === 'type') {
    const aIsBlend = a.blendItems.length > 0;
    const bIsBlend = b.blendItems.length > 0;
    const aIsCoffee = a.coffeeItems.length > 0;
    const bIsCoffee = b.coffeeItems.length > 0;
    const aIsBoth = aIsBlend && aIsCoffee;
    const bIsBoth = bIsBlend && bIsCoffee;

    if (aIsBoth && bIsBoth) return 0;
    if (aIsBoth) return 1;
    if (bIsBoth) return -1;
    if (aIsCoffee && bIsCoffee) return 0;
    if (aIsBlend && bIsBlend) return 0;
    if (aIsCoffee && bIsBlend) return 1;
    if (aIsBlend && bIsCoffee) return -1;
  }
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
};
const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};
const stableSort = (array, comparator) => {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) {
      return order;
    }
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
};

const GET_USER_HISTORY = gql`
  query GetUserHistory($id: ID) {
    getUserHistory(id: $id) {
      user_id
      createdAt
      state
      shippingAddress {
        city
      }
      coffeeItems {
        coffeeId
        pricePerLb
        quantity
        price {
          key
          measurement
          quantity
          price
        }
      }
      ourBlendItems {
        blendId
        pricePerLb
        quantity
        qualities
        price {
          key
          measurement
          quantity
          price
        }
      }
      blendItems {
        blendId
        qualities {
          acidity
          mouthFeel
          caramel
          fruit
          flower
        }
        coffees {
          coffeeId
          percentage
        }
        quantity
        price {
          key
          measurement
          quantity
          price
        }
      }
    }
  }
`;
const headCells = [
  {
    id: 'type',
    numeric: false,
    disablePadding: false,
    label: 'type',
  },
  {
    id: 'createdAt',
    numeric: true,
    disablePadding: false,
    label: 'date',
  },
  {
    id: 'state',
    numeric: false,
    disablePadding: false,
    label: 'status',
  },
];
const EnhancedTableHead = (props) => {
  const { order, orderBy, onRequestSort } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        <TableCell></TableCell>
        {headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'left'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : 'asc'}
              onClick={createSortHandler(headCell.id)}
            >
              {headCell.label}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
};
const Row = (props) => {
  const [open, setOpen] = React.useState(false);
  const classes = useStyles();
  const [coffees] = useGlobal(COFFEES);
  const [ourBlends] = useGlobal(OUR_BLENDS);
  const [ourBlendPurchases, setOurBlendPurchases] = useGlobal(
    OUR_BLENDS_SHOPPING_CART
  );
  const [coffeePurchases, setCoffeePurchases] = useGlobal(
    COFFEES_SHOPPING_CART
  );
  const [customPurchases, setCustomPurchases] = useGlobal(BLEND_SHOPPING_CART);
  const settings = useGlobal(CUSTOM_SETTINGS);
  const { item } = props;
  const generateBlendId = (blend) => {
    const id = blend.reduce((previousValue, currentValue) => {
      const { coffeeId, percentage } = currentValue;
      const blendId = `${coffeeId},${percentage}:`;
      return previousValue + blendId;
    }, '');
    return id;
  };
  const getBlendById = (id) => {
    const ourBlend = ourBlends.find((c) => c._id === id);
    return ourBlend;
  };
  const getCoffeeById = (id) => {
    const coffee = coffees.find((c) => c._id === id);
    return coffee;
  };
  const getCoffeeByKey = (key) => {
    const coffee = coffees.find((c) => c.key === key);
    return coffee;
  };
  const getCoffeeNameByKey = (key) => {
    const coffee = getCoffeeByKey(key);
    return `${coffee.region}- ${coffee.roast}`;
  };
  const getCoffeeNameById = (id) => {
    const coffee = getCoffeeById(id);
    return `${coffee.region}-${coffee.roast}`;
  };
  const getOurBlend = (_id) => {
    return ourBlends.find((c) => c._id === _id);
  };
  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  });
  const handleCoffeeItemAdd = (i) => {
    const { coffeeId, quantity, price } = i;
    const coffee = getCoffeeById(coffeeId);
    let duplicatePurchases = [...coffeePurchases];
    let item = duplicatePurchases.find((p) => p.coffeeId === coffeeId);
    if (!item) {
      item = {
        coffeeId,
        coffee,
        quantity,
        purchaseId: `${coffee.key}-${0}`,
        price: {
          price: price.price,
          quantity: price.quantity,
          measurement: price.measurement,
        },
      };
      duplicatePurchases.push(item);
    } else {
      item.quantity = item.quantity + quantity;
    }

    setCoffeePurchases(duplicatePurchases);
  };
  const handleOurBlendItemAdd = (i) => {
    const { blendId, quantity, price } = i;
    const ourBlend = getBlendById(blendId);
    let duplicatePurchases = [...ourBlendPurchases];
    let item = duplicatePurchases.find((p) => p.blendId === blendId);
    if (!item) {
      item = {
        blendId,
        ourBlend,
        quantity,
        purchaseId: `${blendId}-${0}`,
        price: {
          price: price.price,
          quantity: price.quantity,
          measurement: price.measurement,
        },
      };
      duplicatePurchases.push(item);
    } else {
      item.quantity = item.quantity + quantity;
    }
    setOurBlendPurchases(duplicatePurchases);
  };
  const handleCustomBlendItemAdd = (item) => {
    const duplicatePurchases = [...customPurchases];
    const duplicate = { ...item };
    const { coffees } = duplicate;
    const blendId = generateBlendId(coffees);
    const count = customPurchases.filter((p) => p.blendId === blendId).length;
    const purchaseId = `${blendId}-${count}`;
    duplicate.blendId = blendId;
    duplicate.purchaseId = purchaseId;
    duplicatePurchases.push(duplicate);
    setCustomPurchases(duplicatePurchases);
  };
  const onBuyAgain = (req) => {
    const { item } = req;
    const type = item.__typename;

    switch (type) {
      case 'BlendItem':
        handleCustomBlendItemAdd(item);
        break;
      case 'CoffeeItem':
        handleCoffeeItemAdd(item);
        break;
      case 'OurBlendItem':
        handleOurBlendItemAdd(item);
        break;

      default:
        break;
    }
  };
  const renderType = () => {
    const isCoffees = item.coffeeItems.length > 0;
    const isBlends = item.blendItems.length > 0;
    const isOurBlends = item.ourBlendItems.length > 0;
    let value = '';
    if (isCoffees && isBlends && isOurBlends) {
      value = `coffees, customs, & blends`;
    } else if (isCoffees && isBlends) {
      value = `coffees & customs`;
    } else if (isCoffees && isOurBlends) {
      value = `coffees & blends`;
    } else if (isOurBlends && isBlends) {
      value = `customs & blends`;
    } else if (isCoffees) {
      value = `coffees`;
    } else if (isBlends) {
      value = `customs`;
    } else {
      value = `blends`;
    }

    return <Typography className={classes.type}>{value}</Typography>;
  };
  const renderCustomBlends = () => {
    return item.blendItems.length > 0 ? (
      <Box sx={{ margin: 1 }}>
        <Typography variant="h6" gutterBottom component="div">
          Custom Blend Items
        </Typography>
        <Table size="small" aria-label="purchases">
          <TableHead>
            <TableRow>
              <TableCell>Coffees</TableCell>
              <TableCell align="right">Qty</TableCell>
              <TableCell align="right">Total</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {item.blendItems.map((item, j) => (
              <TableRow key={j}>
                <TableCell component="th" scope="row" key={j}>
                  {item.coffees.map((c, k) => (
                    <span key={k}>{`${getCoffeeNameByKey(c.coffeeId)} ${
                      c.percentage
                    }%, `}</span>
                  ))}
                  <span className={classes.packageSize}>
                    {` ${item.price.quantity}/${item.price.measurement}`}
                  </span>
                </TableCell>
                <TableCell align="right">{`${item.quantity} `}</TableCell>
                <TableCell align="right">
                  {formatter.format(item.quantity * item.price.price)}
                </TableCell>
                <TableCell padding="none" align="right">
                  <Button
                    size="small"
                    className={classes.buyAgainButton}
                    variant="contained"
                    color="primary"
                    onClick={() => onBuyAgain({ type: 'blend', item: item })}
                  >
                    buy again
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Box>
    ) : null;
  };
  const renderOurBlends = () => {
    return item.ourBlendItems.length > 0 ? (
      <Box sx={{ margin: 1 }}>
        <Typography variant="h6" gutterBottom component="div">
          Blend Items
        </Typography>
        <Table size="small" aria-label="purchases">
          <TableHead>
            <TableRow>
              <TableCell>Coffee</TableCell>
              <TableCell align="right">Qty</TableCell>
              <TableCell align="right">Total</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {item.ourBlendItems.map((item, j) => (
              <TableRow key={j}>
                <TableCell component="th" scope="row" key={j}>
                  {getOurBlend(item.blendId).name}
                  <span className={classes.packageSize}>
                    {` ${item.price.quantity}/${item.price.measurement}`}
                  </span>
                </TableCell>
                <TableCell align="right">{`${item.quantity}`}</TableCell>
                <TableCell align="right">
                  {formatter.format(item.quantity * item.price.price)}
                </TableCell>
                <TableCell padding="none" align="right">
                  <Button
                    size="small"
                    className={classes.buyAgainButton}
                    variant="contained"
                    color="primary"
                    onClick={() =>
                      onBuyAgain({
                        type: 'coffee',
                        item: item,
                      })
                    }
                  >
                    buy again
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Box>
    ) : null;
  };
  const renderCoffees = () => {
    return item.coffeeItems.length > 0 ? (
      <Box sx={{ margin: 1 }}>
        <Typography variant="h6" gutterBottom component="div">
          Coffee Items
        </Typography>
        <Table size="small" aria-label="purchases">
          <TableHead>
            <TableRow>
              <TableCell>Coffee</TableCell>
              <TableCell align="right">Qty</TableCell>
              <TableCell align="right">Total</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {item.coffeeItems.map((item, j) => (
              <TableRow key={j}>
                <TableCell component="th" scope="row" key={j}>
                  {getCoffeeNameById(item.coffeeId)}
                  <span className={classes.packageSize}>
                    {` ${item.price.quantity}/${item.price.measurement}`}
                  </span>
                </TableCell>
                <TableCell align="right">{`${item.quantity}`}</TableCell>
                <TableCell align="right">
                  {formatter.format(item.quantity * item.price.price)}
                </TableCell>
                <TableCell padding="none" align="right">
                  <Button
                    size="small"
                    className={classes.buyAgainButton}
                    variant="contained"
                    color="primary"
                    onClick={() =>
                      onBuyAgain({
                        type: 'coffee',
                        item: item,
                      })
                    }
                  >
                    buy again
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Box>
    ) : null;
  };
  return (
    <React.Fragment>
      <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
        <TableCell>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
          >
            {open ? (
              <span role="img" aria-label="open" className={classes.emoji}>
                📂
              </span>
            ) : (
              <span role="img" aria-label="closed" className={classes.emoji}>
                📁
              </span>
            )}
          </IconButton>
        </TableCell>
        <TableCell align="left">{renderType()}</TableCell>
        <TableCell align="right">
          {format(new Date(item.createdAt), 'MM-dd-yyyy h:mm aaaa')}
        </TableCell>
        <TableCell align="right" className={classes.state}>
          {item.state}
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            {renderCustomBlends()}
            {renderCoffees()}
            {renderOurBlends()}
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
};
const History = (props) => {
  const classes = useStyles();
  const domHistory = useHistory();
  const [user] = useGlobal(USER);
  const [coffees] = useGlobal(COFFEES);
  const [history, setHistory] = useState();
  const [order, setOrder] = React.useState('asc');
  const [orderBy, setOrderBy] = React.useState('type');
  const userLoginError = useUserLoginError();
  const [getUserHistory, { loading, error, data }] = useLazyQuery(
    GET_USER_HISTORY,
    {
      fetchPolicy: 'no-cache',
    }
  );
  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };
  const renderTable = () => {
    return (
      <TableContainer component={Paper}>
        <Table
          stickyHeader
          size="small"
          aria-label="history table"
          className={classes.outerTable}
        >
          <EnhancedTableHead
            order={order}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
          />
          <TableBody>
            {stableSort(history, getComparator(order, orderBy)).map(
              (item, i) => {
                return <Row item={item} key={i} />;
              }
            )}
          </TableBody>
        </Table>
      </TableContainer>
    );
  };
  const renderHistory = () => {
    return history && coffees && user ? (
      <div className={classes.history}>{renderTable()}</div>
    ) : null;
  };
  useEffect(() => {
    if (!history && user) {
      getUserHistory({
        variables: {
          id: user._id,
        },
      });
    } else if (history && !user) {
      setHistory(null);
    }
  }, [history, user]);
  useEffect(() => {
    if (data) {
      setHistory(data.getUserHistory);
    }
  }, [data]);
  useEffect(() => {
    if (error) {
      debugger;
      //   setUser(null);
      //   setShowProgress(false);
      //   const err = { message: text.signin.inputIncorrectMsg };
      //   apolloError(error);
    }
  }, [error]);
  useEffect(() => {
    if (!user) domHistory.push('/');
  }, [user]);
  return (
    <div>
      {error ? userLoginError(error) : null}
      <Grid container className={classes.header}>
        <div className={classes.leftHeader}>
          <Sidebar />
        </div>
        <ShoppingCartButton />
        <UserButtons />
      </Grid>
      <div className={classes.menubar}>
        <BackButton />
        <MenuBar dept="history" />
      </div>
      <ShareButton baseStyle={classes.ShareButton} />

      <Grid container className={classes.contents}>
        <HeaderImage />
        <ShareButton baseStyle={classes.shareButton} />
        {renderHistory()}
      </Grid>
      <Copyright />
    </div>
  );
};

export default History;
