import React from 'react';
import useStyles from './styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Slider from '../slider';
const Quality=(props)=>{
	const {title,text,values,onClick, value} = props,
	classes = useStyles();
	return (
		<Grid container className={classes.container} direction="column" spacing={0}>
			<Typography variant='h4' className={classes.title}>
				{title}
			</Typography>
			<Typography className={classes.topParagraph} paragraph variant="body1">
				{text.top}
			</Typography>
			<Typography className={classes.middleParagraph} variant="h6">
				{text.command}
			</Typography>
			<Slider value={value} values={values} onSelect={onClick}/>
			<Typography paragraph className={classes.bottomParagraph} variant='body1'>
				{text.bottom}
			</Typography>
		</Grid>)
}
export default Quality;
