import React, { useGlobal, useEffect, useState } from 'reactn';
import useStyles from './styles';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { CircularProgress } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Field from './field';
import CoffeeItems from '../shoppingCart/coffeeItems';
import BlendItems from '../shoppingCart/blendItems';
import OurBlendItems from '../shoppingCart/ourBlendItems';
import text from '../text';
import BackButton from '../../../backButton';
import useUserLoginError from '../../user/useLoginError';
import {
  USER,
  COFFEES_SHOPPING_CART,
  BLEND_SHOPPING_CART,
  OUR_BLENDS_SHOPPING_CART,
  OUR_BLENDS,
  PAYMENT,
  LAST_COFFEE_ORDER,
  COFFEES,
} from '../../../../constant/coffee';
import { useMutation, gql } from '@apollo/client';
import Copyright from '../../../copyright';

const PLACE_ORDER = gql`
    mutation placeOrder($order: OrderInput) {
      placeOrder(order: $order) {
        _id
        user_id
        createdAt
        updatedAt
        shippingAddress {
          city
          street
          zipcode
          state
          suite
          country
        }
        payment {
          name
          number
          year
          month
          cvc
          billingAddress {
            city
            street
            zipcode
            state
            suite
            country
          }
        }
        blendItems {
          qualities {
            acidity
            mouthFeel
            caramel
            fruit
            flower
          }
          coffees {
            coffeeId
            percentage
            pricePerLb
          }
          quantity
          price {
            measurement
            quantity
            price
          }
        }
        coffeeItems {
          coffeeId
          pricePerLb
          quantity
          price {
            measurement
            quantity
            price
          }
        }
        ourBlendItems {
          blendId
          pricePerLb
          quantity
          price {
            measurement
            quantity
            price
          }
        }
      }
    }
  `,
  Finalize = (props) => {
    const userLoginError = useUserLoginError();
    const [error, setError] = useState();
    const [placeOrder, { loading }] = useMutation(PLACE_ORDER, {
      onCompleted(data) {
        setShowProgress(false);
        setLastOrder(data.placeOrder);
        history.push('/buy/thankyou');
      },
      onError: (err) => {
        setShowProgress(false);
        setError(err);
        debugger;
      },
    });
    const classes = useStyles();
    const history = useHistory();
    const [showProgress, setShowProgress] = useState(false);
    const [coffeeShoppingCart, setCoffeeShoppingCart] = useGlobal(
      COFFEES_SHOPPING_CART
    );
    const [customBlendPurchases, setCustomBlendPurchases] =
      useGlobal(BLEND_SHOPPING_CART);
    const [ourBlendPurchases, setOurBlendPurchases] = useGlobal(
      OUR_BLENDS_SHOPPING_CART
    );
    const [user] = useGlobal(USER);
    const [lastOrder, setLastOrder] = useGlobal(LAST_COFFEE_ORDER);
    const [coffees] = useGlobal(COFFEES);
    const [ourBlends] = useGlobal(OUR_BLENDS);
    const [payment] = useGlobal(PAYMENT);
    const findCoffee = (key) => {
      return coffees.find((c) => c.key === key);
    };
    const findOurBlend = (_id) => {
      return ourBlends.find((c) => c._id === _id);
    };
    const buildBlendMutation = () => {
      if (customBlendPurchases) {
        return customBlendPurchases.map((p) => {
          const { coffees, qualities, quantity, price, blendId } = p;
          const { fruit, acidity, mouthFeel, caramel, flower } = qualities;
          const coffs = coffees.map((blend) => {
            const { key, percentage, coffeeId } = blend;
            return {
              coffeeId: key ? key : coffeeId,
              percentage,
            };
          });
          return {
            qualities: {
              acidity,
              mouthFeel,
              caramel,
              fruit,
              flower,
            },
            coffees: coffs,
            blendId,
            quantity,
            price: {
              measurement: price.measurement,
              price: price.price,
              quantity: price.quantity,
            },
          };
        });
      } else {
        return [];
      }
    };
    const buildCoffeeMutation = () => {
      const filtered = coffeeShoppingCart.filter((p) => p.quantity > 0);
      return filtered.map((purchase) => {
        const { coffee, quantity, price } = purchase;
        const coffeeId = coffee._id;
        return {
          coffeeId,
          price: {
            measurement: price.measurement,
            price: price.price,
            quantity: price.quantity,
          },
          quantity: quantity,
        };
      });
    };
    const buildOurBlendMutation = () => {
      const filtered = ourBlendPurchases.filter((p) => p.quantity > 0);
      return filtered.map((purchase) => {
        const { ourBlend, quantity, price } = purchase;
        return {
          blendId: ourBlend._id,
          quantity,
          price: {
            measurement: price.measurement,
            price: price.price,
            quantity: price.quantity,
          },
        };
      });
    };
    const onPlaceOrder = () => {
      const blendItems = buildBlendMutation();
      const coffeeItems = buildCoffeeMutation();
      const ourBlendItems = buildOurBlendMutation();

      placeOrder({
        variables: {
          order: {
            user_id: user._id,
            shippingAddress: {
              city: user.address.city,
              street: user.address.street,
              zipcode: user.address.zipcode,
              state: user.address.state,
              suite: user.address.suite,
              country: user.address.country,
            },
            payment: {
              name: payment.name,
              number: payment.number,
              month: payment.month,
              year: payment.year,
              cvc: payment.cvc,
              billingAddress: {
                city: user.address.city,
                street: user.address.street,
                zipcode: user.address.zipcode,
                state: user.address.state,
                suite: user.address.suite,
                country: user.address.country,
              },
            },
            blendItems: blendItems,
            coffeeItems: coffeeItems,
            ourBlendItems: ourBlendItems,
          },
        },
      });
    };
    const renderUser = () => {
      return user ? (
        <Paper className={classes.infoContainer}>
          <Typography className={classes.sectionTitle} variant="subtitle1">
            {text.buy.finalize.titleUser}
          </Typography>
          <Grid container spacing={0} className={classes.infoFields}>
            {<Field label="name" value={user.name} />}
          </Grid>
        </Paper>
      ) : null;
    };
    const renderPayment = () => {
      return (
        <Paper className={classes.infoContainer}>
          <Typography className={classes.sectionTitle} variant="subtitle1">
            {text.buy.finalize.titlePayment}
          </Typography>
          <Grid container spacing={0} className={classes.infoFields}>
            {Object.entries(payment)
              .filter((x) => x[0] !== 'user_id' && x[0] !== '__typename')
              .map((e, i) => (
                <Field label={e[0]} value={e[1]} key={i} />
              ))}
          </Grid>
        </Paper>
      );
    };
    const renderLabel = (label) => {
      return label
        .replace(/([A-Z])/g, ' $1')
        .trim()
        .toLowerCase();
    };
    const renderCustomerSection = (area) => {
      const entities = Object.entries(user[area]);
      const filtered = entities.filter((x) => {
        return x[1] && x[1].length > 0 && x[0] !== '__typename';
      });
      return filtered.map((e, i) => {
        return <Field label={e[0]} value={e[1]} key={i} />;
      });
    };
    const renderCustomerInfo = (area) => {
      return user.hasOwnProperty(area) ? (
        <Paper className={classes.infoContainer}>
          <Typography className={classes.sectionTitle} variant="subtitle1">
            {renderLabel(area)}
          </Typography>
          <Grid container spacing={0} className={classes.infoFields}>
            {renderCustomerSection(area)}
          </Grid>
        </Paper>
      ) : null;
    };
    const renderPlaceOrderButton = () => {
      return (
        <Button
          fullWidth={true}
          variant="contained"
          color="primary"
          size="large"
          className={classes.button}
          onClick={onPlaceOrder}
        >
          {text.buy.finalize.placeOrderButton}
        </Button>
      );
    };
    const render = () => {
      return (
        <div>
          {error ? userLoginError(error) : null}
          {showProgress && <CircularProgress className={classes.progress} />}
          <Grid container className={classes.header}>
            <BackButton />
          </Grid>

          <Grid
            container
            direction="column"
            spacing={0}
            wrap="nowrap"
            className={classes.contents}
          >
            {renderPlaceOrderButton()}
            {renderUser()}
            {renderCustomerInfo('contactInfo')}
            {renderCustomerInfo('address')}
            {renderCustomerInfo('shippingAddress')}
            {renderPayment()}
            <Paper elevation={3}>
              <Typography className={classes.sectionTitle} variant="subtitle1">
                Items
              </Typography>
              {coffeeShoppingCart.length > 0 ? <CoffeeItems /> : null}
              {customBlendPurchases.length > 0 ? <BlendItems /> : null}
              {ourBlendPurchases.length > 0 ? <OurBlendItems /> : null}
            </Paper>
          </Grid>
          <Copyright />
        </div>
      );
    };
    useEffect(() => {
      return () => {
        setCustomBlendPurchases([]);
        setCoffeeShoppingCart([]);
        setOurBlendPurchases([]);
      };
    }, []);
    useEffect(() => {
      if (loading) setShowProgress(true);
    }, [loading]);
    useEffect(() => {
      if (
        !(
          user &&
          payment &&
          (coffeeShoppingCart.length > 0 ||
            customBlendPurchases.length > 0 ||
            ourBlendPurchases.length > 0)
        )
      ) {
        history.push('/');
      }
    }, [
      user,
      coffeeShoppingCart,
      customBlendPurchases,
      payment,
      ourBlendPurchases,
    ]);
    return user &&
      payment &&
      (coffeeShoppingCart.length > 0 ||
        customBlendPurchases.length > 0 ||
        ourBlendPurchases.length > 0)
      ? render()
      : null;
  };
export default Finalize;
