import React, { useGlobal, useEffect } from 'reactn';
import useStyles from './styles';
import { Paper, Grid, Button, Typography } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import { USER, LAST_COFFEE_ORDER } from '../../../../constant/coffee';
import Address from './address';
import CoffeeItems from './items/coffeeItems';
import BlendItems from './items/blendItems';
import OurBlendItems from './items/ourBlendItems';
import text from '../text';
import Copyright from '../../../copyright';

const ThankYou = (props) => {
  const classes = useStyles();
  const history = useHistory();
  const [user] = useGlobal(USER);
  const [lastOrder] = useGlobal(LAST_COFFEE_ORDER);
  const onContinue = () => {
    history.push('/');
  };
  const renderContinueButton = () => {
    return (
      <Button
        fullWidth={true}
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={onContinue}
      >
        {text.thankyou.continue}
      </Button>
    );
  };
  const moneyFormatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  });
  const renderTotal = () => {
    const { blendItems, coffeeItems, ourBlendItems } = lastOrder;
    const blendTotal = blendItems.reduce(
      (previous, current) => previous + current.price.price * current.quantity,
      0
    );
    const coffeeTotal = coffeeItems.reduce(
      (previous, current) => previous + current.price.price * current.quantity,
      0
    );
    const ourBlendsTotal = ourBlendItems.reduce(
      (previous, current) => previous + current.pricePerLb * current.quantity,
      0
    );
    const total = blendTotal + coffeeTotal + ourBlendsTotal;

    return (
      <Paper elevation={3} className={classes.total}>
        <Typography
          className={classes.totalTypology}
        >{`total ${moneyFormatter.format(total)}`}</Typography>
      </Paper>
    );
  };
  const renderOrder = () => {
    console.log(lastOrder);
    const { coffeeItems, blendItems, ourBlendItems } = lastOrder;
    return (
      <Grid container className={classes.order}>
        <Address />
        {renderTotal()}
        <div className={classes.items}>
          {blendItems.length === 0 ? null : <BlendItems items={blendItems} />}
          {coffeeItems.length === 0 ? null : (
            <CoffeeItems items={coffeeItems} />
          )}
          {ourBlendItems.length === 0 ? null : (
            <OurBlendItems items={ourBlendItems} />
          )}
        </div>
      </Grid>
    );
  };
  useEffect(() => {
    if (!user || !lastOrder) {
      history.push('/');
    }
  });
  return user || lastOrder ? (
    <div className={classes.page}>
      <Grid container className={classes.content}>
        {renderContinueButton()}
        <Typography className={classes.title}>
          {text.thankyou.title} {user.name}
        </Typography>

        {renderOrder()}
      </Grid>
      <Copyright />
    </div>
  ) : null;
};
export default ThankYou;
