import React, { useGlobal } from 'reactn';
import useStyles from './styles';
import { Grid, Button, Typography } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import { DECAF } from '../../../../constant/coffee';
import BackButton from '../../../backButton';
import Sidebar from '../../../sidebar';
import UserButtons from '../../user/buttons';
import ShoppingCartButton from '../../buy/shoppingCart/button';
import text from '../text';
import HeaderImg from '../../../headerImg';
import MenuBar from '../../../menuBar';
import Copyright from '../../../copyright';

const Decaf = (props) => {
  const classes = useStyles();
  const [_decaf_, setDecaf] = useGlobal(DECAF);
  const history = useHistory();
  const onClick = (decaf) => {
    history.push('/customblend/mouthfeel');
    setDecaf(decaf);
  };
  const renderInput = () => {
    return (
      <div className={classes.inputs}>
        <Button
          onClick={() => onClick(true)}
          variant="contained"
          className={classes.decafButton}
          color="primary"
        >
          Decaf
        </Button>
        <Button
          onClick={() => onClick(false)}
          variant="contained"
          className={classes.regularButton}
          color="primary"
        >
          Regular
        </Button>
      </div>
    );
  };
  return (
    <div className={classes.page}>
      <Grid container className={classes.header}>
        <div>
          <Sidebar />
        </div>
        <ShoppingCartButton />
        <UserButtons />
      </Grid>
      <div className={classes.menubar}>
        <BackButton />
        <MenuBar dept="custom" />
      </div>
      <HeaderImg size="small" />
      <div className={classes.container}>
        <Typography variant="h4" className={classes.title}>
          {text.decaf.title}
        </Typography>
        <Typography className={classes.topParagraph} variant="body1">
          {text.decaf.top}
        </Typography>
        <Typography className={classes.middleParagraph} variant="h6">
          {text.decaf.command}
        </Typography>
        {renderInput()}
        <Typography className={classes.bottomParagraph} variant="body1">
          {text.decaf.bottom}
        </Typography>
      </div>
      <Copyright />
    </div>
  );
};
export default Decaf;
