import React, {useGlobal,useEffect,} from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {useHistory} from "react-router-dom";
import {MOUTHFEEL,VARIETAL} from '../../../../constant/learning';
import sliderValues from './steps';
import Quality from '../quality';
import BackButton from '../../../backButton';
import text from '../text';
const Mouthfeel=(props)=>{
	const classes = useStyles(),
	[mouthFeel, setMouthFeel]= useGlobal(MOUTHFEEL),
	[varietal,]= useGlobal(VARIETAL),
	history=useHistory(),
	onClick=(mf)=>{
		history.push('/learning/acidity');
		setMouthFeel(mf);
	};
	useEffect(()=>{
		if(!varietal){
			history.push('/learning');
		};
	},[]);
	return (
		<div className={classes.page}>
			<Grid container className={classes.header}>
				<BackButton gotTo='/learning'/>
			</Grid>
			<Typography 
				variant='h5' 
				gutterBottom
				align='center'>
					{varietal&&`${varietal.region}-${varietal.roast}`}
			</Typography>
			<Quality
				value={mouthFeel}
				title='🍋 Mouthfeel 🍦'
				text={text.mouthfeel}
				values={sliderValues}
				onClick={onClick}/>
		</div>
	);
};
export default Mouthfeel;
