import React, { useGlobal, useState } from 'reactn';
import useStyles from './styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';

import decafIcon from '../../../../../images/decaf.png';
import notDecafIcon from '../../../../../images/notDecaf.png';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import Purchase from '../../../ourBlends/purchase';
import {
  OUR_BLENDS_SHOPPING_CART,
  OUR_BLENDS,
} from '../../../../../constant/coffee';
//import text from '../text';
const Items = (props) => {
  const classes = useStyles();
  const [ourBlendItems] = useGlobal(OUR_BLENDS);
  const [deletePurchaseId, setDeletePurchaseId] = useState('');
  const [purchases, setPurchases] = useGlobal(OUR_BLENDS_SHOPPING_CART);
  const [dialogOpen, setDialogOpen] = useState(false);
  const findItem = (_id) => {
    return purchases.find((i) => i._id === _id);
  };
  const onQtyChange = (item, quantity) => {
    findItem(item._id).quantity = quantity;
    setPurchases(purchases);
  };
  const onPurchaseChange = (event) => {
    const { type, ourBlend, purchaseId, price, quantity } = event;
    const _purchases = [...purchases];
    const purchase = _purchases.find(
      (purchase) => purchase.purchaseId === purchaseId
    );
    if (purchase) {
      purchase.quantity = quantity;
      purchase.price = price;
    } else {
      _purchases.push({
        ourBlend,
        quantity,
        price,
        purchaseId,
        quantity,
      });
    }
    setPurchases(_purchases);
  };
  const onDelete = () => {
    const ourBlendPurchases = purchases.filter(
      (purchase) => purchase.purchaseId != deletePurchaseId
    );
    setPurchases(ourBlendPurchases);
    setDialogOpen(false);
  };
  const onDeleteClick = (id) => {
    setDeletePurchaseId(id);
    setDialogOpen(true);
  };

  const renderPurchase = (purchase) => {
    return (
      <Purchase
        key={Math.random()}
        purchaseId={purchase.purchaseId}
        quantity={purchase.quantity}
        onDelete={onDeleteClick}
        price={purchase.price}
        onPurchaseChange={onPurchaseChange}
        ourBlend={purchase.ourBlend}
      />
    );
  };
  const findPurchases = (purchase, purchases) => {
    const result = purchases.filter((p) => {
      return purchase.ourBlend === p.ourBlend;
    });
    return result;
  };
  const renderItems = () => {
    const ourBlendPurchases = purchases.filter((p) => p.quantity > 0);
    const ourBlends = ourBlendPurchases.reduce((acc, purchase) => {
      if (
        acc.find((p) => p.ourBlend._id === purchase.ourBlend._id) === undefined
      ) {
        acc.push(purchase);
      }
      return acc;
    }, []);
    return ourBlends.map((purchase, i) => {
      return (
        <Paper elevation={4} className={classes.item} key={i}>
          <div className={classes.desc}>
            <Typography className={classes.ourBlendTitle}>
              {purchase.ourBlend.name}{' '}
            </Typography>
            <img
              className={classes.decafIcon}
              src={purchase.ourBlend.decaf === true ? decafIcon : notDecafIcon}
            />
          </div>
          {renderPurchases(ourBlendPurchases, purchase)}
        </Paper>
      );
    });
  };
  const renderPurchases = (ourBlendPurchases, ourBlend) => {
    const purchases = findPurchases(ourBlend, ourBlendPurchases);
    return (
      <div className={classes.purchases}>
        {purchases.map((purchase, i) => renderPurchase(purchase))}
      </div>
    );
  };
  const renderDialog = () => {
    return (
      <Dialog
        open={dialogOpen}
        onClose={() => setDialogOpen(false)}
        aria-labelledby="alert-dialog-title"
      >
        <DialogTitle id="remove-dialog-title">
          Remove this item from your cart?
        </DialogTitle>
        <DialogActions>
          <Button onClick={() => setDialogOpen(false)} color="primary">
            Cancel
          </Button>
          <Button onClick={onDelete} color="primary">
            Yes
          </Button>
        </DialogActions>
      </Dialog>
    );
  };
  return purchases && purchases.length > 0 ? (
    <Paper elevation={4} className={classes.content}>
      {renderDialog()}
      <Typography className={classes.title}>Our Blends</Typography>
      {renderItems()}
    </Paper>
  ) : null;
};
export default Items;
