import React, { useGlobal } from 'reactn';
import { useHistory } from 'react-router-dom';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCartOutlined';
import {
  COFFEES_SHOPPING_CART,
  BLEND_SHOPPING_CART,
  OUR_BLENDS_SHOPPING_CART,
} from '../../../../../constant/coffee';
const ShoppingCartButton = () => {
  const history = useHistory();
  const [coffeeShoppingCart] = useGlobal(COFFEES_SHOPPING_CART);
  const [blendShoppingCart] = useGlobal(BLEND_SHOPPING_CART);
  const [ourBlendsShoppingCart] = useGlobal(OUR_BLENDS_SHOPPING_CART);
  const onClick = () => {
    //  console.log(dept);
    history.push(`/buy/shoppingcart`);
  };
  const qty = () => {
    let blendTotal = 0;
    if (blendShoppingCart) blendTotal = blendShoppingCart.length;
    let q = 0;
    if (coffeeShoppingCart)
      coffeeShoppingCart.forEach((p) => {
        if (p.quantity > 0) q++;
      });
    if (ourBlendsShoppingCart)
      ourBlendsShoppingCart.forEach((p) => {
        if (p.quantity > 0) q++;
      });
    return q + blendTotal;
  };
  return (
    <IconButton onClick={onClick} disabled={qty() === 0} color="primary">
      <ShoppingCartIcon fontSize="large" />
      <Typography variant="caption">
        {qty()} {qty() === 1 ? 'item' : 'items'}
      </Typography>
    </IconButton>
  );
};
export default ShoppingCartButton;
