import React, {useGlobal,useEffect} from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {useHistory} from "react-router-dom";
import BackButton from '../../../backButton';
import {MOUTHFEEL,ACIDITY,CARAMEL,VARIETAL} from '../../../../constant/learning';
import sliderValues from './steps';
import Quality from '../quality';
import text from '../text'
const Caramel=(props)=>{
	const classes = useStyles(),
	history=useHistory(),
	[mouthFeel]= useGlobal(MOUTHFEEL),
	[acidity,]= useGlobal(ACIDITY),
	[varietal,]= useGlobal(VARIETAL),
	[caramel,setCaramel]=useGlobal(CARAMEL),
	onClick=(caramel)=>{
		history.push('/learning/fruit')
		setCaramel(caramel)
	};
	useEffect(()=>{
		if(!varietal || !mouthFeel || !acidity){
			history.push('/learning/');
		};
	},[]);
	return (
		<div className={classes.page}>
		<Grid container className={classes.header}>
			<BackButton />
		</Grid>
		<Typography 
				variant='h5' 
				gutterBottom
				align='center'>
					{varietal&&`${varietal.region}-${varietal.roast}`}
			</Typography>
		<Quality
			value={caramel}
			title='🍫 Caramel 🌰'
			text={text.caramel}
			values={sliderValues}
			onClick={onClick}/>
		</div>)
};
export default Caramel;
