import React, { useState } from 'reactn';
import useStyles from './styles';
import { useHistory } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Icon from '@material-ui/core/Icon';
import version from '../../constant/version';
const sidebarItems = [
  ['About Purfect Blend', '/about', '🤓'],
  ['Home', '/', '🏠'],
  ['Find your blend', '/customblend/mouthfeel', '🕵🏻‍♂️'],
  ['Our Blends', '/ourblends', '😊'],
  ['Coffees', '/coffees', '☕️'],
  ['History', '/history', '⏳'],
  ['Privacy policy', '/privacy/policy', '📕'],
  ['Terms of Use', '/privacy/tofu', '📘'],
];
const Sidebar = (props) => {
  const classes = useStyles();
  const [showSidebar, setShowSidebar] = useState(false);
  const history = useHistory();
  const toggleDrawer = (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }
    setShowSidebar(!showSidebar);
  };
  const renderSidebar = () => {
    return (
      <Drawer open={showSidebar} onClose={toggleDrawer}>
        <div
          className={classes.list}
          role="presentation"
          onClick={toggleDrawer}
          onKeyDown={toggleDrawer}
        >
          <List dense disablePadding>
            <ListItem button key={-5}>
              <Icon color="primary" className={classes.sidebarIcon}>
                fingerprint
              </Icon>
              <Typography
                variant="subtitle1"
                color="primary"
              >{`Version ${version}`}</Typography>
            </ListItem>
            {sidebarItems.map((item, index) => (
              <ListItem
                button
                key={index}
                onClick={() => {
                  history.push(item[1]);
                }}
              >
                <span color="primary" className={classes.sidebarIcon}>
                  {item[2]}
                </span>
                <Typography variant="subtitle1" color="primary">
                  {item[0]}
                </Typography>
              </ListItem>
            ))}
          </List>
        </div>
      </Drawer>
    );
  };
  return (
    <>
      <IconButton onClick={toggleDrawer}>
        <span className={classes.emoji}>📚</span>
      </IconButton>
      {renderSidebar()}
    </>
  );
};
export default Sidebar;
