import React, { useGlobal, useState, useEffect } from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useHistory, useLocation } from 'react-router-dom';
import BackButton from '../../../backButton';
import CircularProgress from '@material-ui/core/CircularProgress';
import ContactInfo from '../../user/components/contactInfo';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Address from '../../user/components/address';
import {
  USER,
  COFFEES_SHOPPING_CART,
  BLEND_SHOPPING_CART,
  OUR_BLENDS_SHOPPING_CART,
} from '../../../../constant/coffee';
import HeaderImg from '../../../headerImg';
import { useMutation, gql } from '@apollo/client';
import useLoginError from '../../../utils/useLoginError';
import Copyright from '../../../copyright';

import text from '../text';
const UPDATE_USER = gql`
  mutation UpdateUser($input: UserInput) {
    updateUser(input: $input) {
      name
      _id
      address {
        street
        city
        state
        zipcode
        country
      }
      contactInfo {
        firstName
        lastName
        middleName
        businessName
        email
        phone
      }
    }
  }
`;
const Checkout = (props) => {
  const [error, setError] = useState();
  const loginError = useLoginError();
  const [updateUser, { loading: mutationLoading }] = useMutation(UPDATE_USER, {
    onCompleted(data) {
      setShowProgress(false);
      setUser(data.updateUser);
      history.push('/buy/payment', { next: dept });
    },
    onError: (err) => {
      debugger;
      setShowProgress(false);
      setError(err);
    },
  });
  const classes = useStyles();
  const history = useHistory();
  const location = useLocation();
  const [dept] = useState(location.state.next);
  const [showProgress, setShowProgress] = useState(false);
  const [user, setUser] = useGlobal(USER);
  const [coffeeShoppingCart] = useGlobal(COFFEES_SHOPPING_CART);
  const [blendShoppingCart] = useGlobal(BLEND_SHOPPING_CART);
  const [ourBlendShoppingCart] = useGlobal(OUR_BLENDS_SHOPPING_CART);
  const [localUser, setLocalUser] = useState({ ...user });
  const [contactInfo, setContactInfo] = useState({});
  const [address, setAddress] = useState({});
  const [shippingAddress, setShippingAddress] = useState({});
  const [shippingSame, setShippingSame] = useState(false);
  const onValidAddress = (address) => {
    setLocalUser({ ...localUser, address });
    setAddress(address);
  };
  const onValidShippingAddress = (address) => {
    setShippingAddress(address);
  };
  const onValidContactInfo = (info) => {
    setLocalUser({ ...localUser, contactInfo });
    setContactInfo(info);
  };
  const onContinue = () => {
    setShowProgress(true);
    updateUser({
      variables: {
        input: {
          _id: localUser._id,
          contactInfo: {
            firstName: contactInfo.firstName,
            lastName: contactInfo.lastName,
            middleName: contactInfo.middleName,
            phone: contactInfo.phone,
            email: contactInfo.email,
            businessName: contactInfo.businessName,
          },
          address: {
            city: address.city,
            street: address.street,
            zipcode: address.zipcode,
            state: address.state,
            suite: address.suite,
            country: address.country,
          },
        },
      },
    });
  };
  const renderContinueButton = () => {
    const validAddress = address && Object.keys(address).length,
      validContactInfo = contactInfo && Object.keys(contactInfo).length,
      validShippingAddress = shippingSame || shippingAddress,
      valid = validAddress && validContactInfo && validShippingAddress;
    return valid ? (
      <Button
        variant="contained"
        color="primary"
        size="large"
        fullWidth
        className={classes.button}
        onClick={onContinue}
      >
        continue...
      </Button>
    ) : null;
  };
  const renderShippingSame = () => {
    return (
      <FormControlLabel
        control={
          <Checkbox
            checked={shippingSame}
            onChange={onShippingSame}
            color="primary"
          />
        }
        label="shipping address is the same"
      />
    );
  };
  const onShippingSame = (event) => {
    setShippingSame(event.target.checked);
  };
  const renderShippingAddress = () => {
    const initial =
      localUser && localUser.shippingAddress ? localUser.shippingAddress : null;
    return !shippingSame ? (
      <Address
        initial={initial}
        prefix={text.buy.checkout.addressShippingPrefix}
        onValid={onValidShippingAddress}
      />
    ) : null;
  };
  const renderBillingAddress = () => {
    const initial = localUser && localUser.address ? localUser.address : null;
    return (
      <Address
        initial={initial}
        prefix={text.buy.checkout.addressBillingPrefix}
        onValid={onValidAddress}
      />
    );
  };
  useEffect(() => {
    if (mutationLoading) setShowProgress(true);
  }, [mutationLoading]);
  useEffect(() => {
    setShippingSame(localUser && localUser.shippingAddress ? false : true);
  }, [localUser]);

  useEffect(() => {
    if (
      coffeeShoppingCart.length === 0 &&
      blendShoppingCart.length === 0 &&
      ourBlendShoppingCart.length === 0
    ) {
      history.push('/');
    }
  }, [history, blendShoppingCart, coffeeShoppingCart, ourBlendShoppingCart]);

  return (
    <>
      {error ? loginError(error) : null}
      {showProgress && <CircularProgress className={classes.progress} />}
      <div className={classes.page}>
        <Grid container className={classes.header}>
          <BackButton />
        </Grid>
        <HeaderImg size="small" />
        {showProgress && <CircularProgress className={classes.progress} />}
        <Grid
          container
          direction="column"
          spacing={0}
          wrap="nowrap"
          className={classes.content}
        >
          {renderContinueButton()}
          <ContactInfo onValid={onValidContactInfo} />
          {renderBillingAddress()}
          {renderShippingSame()}
          {renderShippingAddress()}
        </Grid>
      </div>
      <Copyright />
    </>
  );
};
export default Checkout;
