import React, { useGlobal } from 'reactn';
import useStyles from './styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import { COFFEES } from '../../../../../constant/coffee';
import text from '../../text';
const currency = new Intl.NumberFormat('en-US', {
  style: 'currency',
  currency: 'USD',
});
const Items = (props) => {
  const classes = useStyles();
  const [coffees] = useGlobal(COFFEES);
  const getCoffee = (id) => {
    return coffees.find((c) => c._id === id);
  };
  const renderPrice = (purchase) => {
    const { price, quantity } = purchase;
    return (
      <div className={classes.priceContainer}>
        <Typography className={classes.price}>
          {`${quantity} item${quantity > 1 ? 's ' : ' '} @ ${currency.format(
            price.price
          )}/ ${price.quantity} ${price.measurement} = `}
        </Typography>
        <Typography className={classes.priceTotal}>
          {`${currency.format(price.price * quantity)}`}
        </Typography>
      </div>
    );
  };
  const renderItems = () => {
    return props.items.map((item, i) => {
      const coffee = getCoffee(item.coffeeId);
      const price = renderPrice(item);
      return (
        <Paper elevation={3} className={classes.item} key={i}>
          <Typography className={classes.packageSize}>
            {item.price.quantity} {item.price.measurement}
          </Typography>
          <Typography className={classes.desc}>
            {`${coffee.region} ${coffee.roast}`}
          </Typography>
          {price}
        </Paper>
      );
    });
  };
  return (
    <Paper elevation={3} className={classes.content}>
      <Typography className={classes.title}>
        {text.thankyou.items.coffeeTitle}
      </Typography>
      {renderItems()}
    </Paper>
  );
};
export default Items;
