import React, { useGlobal } from 'reactn';
import { Button } from '@material-ui/core';
import { useHistory } from 'react-router';
import useStyles from './styles';
import { USER } from '../../constant/coffee';
import text from '../../text';
const MenuBar = (props) => {
  const classes = useStyles();
  const history = useHistory();
  const [user /*setUser*/] = useGlobal(USER);
  const { dept } = props;
  const renderButtons = () => {
    switch (dept) {
      case 'custom':
        return (
          <>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/coffees')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonCoffees}
            </Button>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/ourblends')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonBlends}
            </Button>
          </>
        );
      case 'blends':
        return (
          <>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/coffees')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonCoffees}
            </Button>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/customblend')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonCustom}
            </Button>
          </>
        );
      case 'coffees':
        return (
          <>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/ourblends')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonBlends}
            </Button>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/customblend')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonCustom}
            </Button>
          </>
        );
      case 'home':
        return (
          <>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/coffees')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonCoffees}
            </Button>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/ourblends')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonBlends}
            </Button>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/customblend')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonCustom}
            </Button>
          </>
        );
      case 'privacy':
        return (
          <>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonHome}
            </Button>
          </>
        );
      default:
        return (
          <>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/coffees')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonCoffees}
            </Button>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/ourblends')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonBlends}
            </Button>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/customblend')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonCustom}
            </Button>
            <Button
              className={classes.button}
              size="small"
              onClick={() => history.push('/')}
              color="primary"
              variant="outlined"
            >
              {text.menubar.buttonHome}
            </Button>
          </>
        );
    }
  };
  return (
    <div className={classes.container}>
      {renderButtons()}
      {user && dept !== 'history' ? (
        <Button
          color="primary"
          fullWidth={false}
          variant="outlined"
          size="small"
          onClick={() => {
            history.push('/history');
          }}
        >
          {text.menubar.buttonHistory}
        </Button>
      ) : null}
    </div>
  );
};

export default MenuBar;
