import React, { useState } from 'reactn';
import useStyles from './styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import classNames from 'classnames';
import Slider from '@material-ui/core/Slider';
import text from '../text';
const CoffeeSlider = (props) => {
  const { values, onSelect, value } = props;
  const classes = useStyles();
  const [slideValue, setSlider] = useState(
    value ? Math.round(value * 100) : 50
  );
  const onChange = (x, y) => {
    setSlider(y ? Math.round(y) : 1);
  };
  const renderImages = () => {
    return (
      <Grid container className={classes.images}>
        {values.map((v, i) => (
          <Grid key={i} container className={classes.imgGrid}>
            <Typography className={classes.text} variant="subtitle1">
              {v.text.top}
            </Typography>
            <img
              key={i}
              alt="slider"
              onClick={() => setSlider(v.step)}
              className={classes.img}
              src={v.image}
            />
            <Typography
              className={classNames(classes.spacer, classes.text)}
              variant="subtitle1"
            >
              {v.text.bottom}
            </Typography>
          </Grid>
        ))}
      </Grid>
    );
  };
  return (
    <Grid container className={classes.grid}>
      <Typography variant="h5" className={classes.selection}>{`${slideValue}/${
        100 - slideValue
      }`}</Typography>
      {renderImages()}
      <Slider
        classes={{ container: classes.slider }}
        value={slideValue}
        aria-labelledby="label"
        min={1}
        max={100}
        step={5}
        onChange={onChange}
      />
      {slideValue !== undefined ? (
        <Button
          onClick={() => onSelect(slideValue / 100)}
          color="primary"
          fullWidth
          className={classes.button}
          variant="contained"
        >
          {text.slider.button}
        </Button>
      ) : null}
    </Grid>
  );
};
export default CoffeeSlider;
