import React, { useGlobal, useEffect } from 'reactn';
import useStyles from './styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import BackButton from '../../../backButton';
import classNames from 'classnames';
import { useMutation, gql } from '@apollo/client';
import {
  VARIETAL,
  MOUTHFEEL,
  ACIDITY,
  CARAMEL,
  FRUIT,
  FLOWER,
} from '../../../../constant/learning';
import { mouthfeelLookup } from '../mouthfeel/steps';
import { acidityLookup } from '../acidity/steps';
import { caramelLookup } from '../caramel/steps';
import { flowerLookup } from '../flower/steps';
import { fruitLookup } from '../fruit/steps';
import HeaderImg from '../../../headerImg';

const ADD_LEARNING_ITEM = gql`
    mutation addLearningItem($item: LearningItemInput) {
      addLearningItem(item: $item) {
        key
        mouthfeel
        acidity
        fruit
        flower
        caramel
        createdAt
        _id
      }
    }
  `,
  Profile = (props) => {
    const [addLearningItem, { loading: mutationLoading }] = useMutation(
        ADD_LEARNING_ITEM,
        {
          onCompleted(data) {
            clearQualities();
            history.push('/learning/thankyou');
          },
          onError: (err) => {
            console.log(err);
            debugger;
            history.push('/learning/thankyou');
          },
        }
      ),
      classes = useStyles(),
      history = useHistory(),
      [varietal, setVarietal] = useGlobal(VARIETAL),
      [acidity, setAcidity] = useGlobal(ACIDITY),
      [mouthfeel, setMouthFeel] = useGlobal(MOUTHFEEL),
      [caramel, setCaramel] = useGlobal(CARAMEL),
      [fruit, setFruit] = useGlobal(FRUIT),
      [flower, setFlower] = useGlobal(FLOWER),
      isStable = () => {
        return varietal && mouthfeel && acidity && caramel && fruit && flower;
      },
      renderMouthfeel = () => {
        const sorted = Object.entries(
          mouthfeelLookup.run({ step: mouthfeel })
        ).sort((a, b) => b[1] - a[1]);
        return (
          <Typography className={classes.quality}>
            <span className={classNames(classes.label, classes.capitalize)}>
              body:
            </span>
            {`  ${sorted[0][0]} - ${Math.round(mouthfeel * 100)}%`}
          </Typography>
        );
      },
      renderAcidity = () => {
        const sorted = Object.entries(
          acidityLookup.run({ step: acidity })
        ).sort((a, b) => b[1] - a[1]);
        return (
          <Typography className={classNames(classes.quality)}>
            <span className={classNames(classes.label, classes.capitalize)}>
              acidity:
            </span>
            {` ${sorted[0][0]} - ${Math.round(acidity * 100)}%`}
          </Typography>
        );
      },
      renderCaramel = () => {
        const sorted = Object.entries(
          caramelLookup.run({ step: caramel })
        ).sort((a, b) => b[1] - a[1]);
        return (
          <Typography className={classNames(classes.quality)}>
            <span className={classNames(classes.label, classes.capitalize)}>
              caramel:
            </span>
            {` ${sorted[0][0]} - ${Math.round(caramel * 100)}%`}
          </Typography>
        );
      },
      renderFruit = () => {
        const sorted = Object.entries(fruitLookup.run({ step: fruit })).sort(
          (a, b) => b[1] - a[1]
        );
        return (
          <Typography className={classes.quality}>
            <span className={classNames(classes.label, classes.capitalize)}>
              fruit:
            </span>
            {` ${sorted[0][0]} - ${Math.round(fruit * 100)}%`}
          </Typography>
        );
      },
      renderFlower = () => {
        const sorted = Object.entries(flowerLookup.run({ step: flower })).sort(
          (a, b) => b[1] - a[1]
        );
        return (
          <Typography className={classes.quality}>
            <span className={classNames(classes.label, classes.capitalize)}>
              floral:
            </span>
            {` ${sorted[0][0]} - ${Math.round(flower * 100)}%`}
          </Typography>
        );
      },
      renderResults = () => {
        return (
          <Grid container className={classes.results}>
            <Typography
              className={classes.coffee}
            >{`${varietal.region} - ${varietal.roast}`}</Typography>
            {renderMouthfeel()}
            {renderAcidity()}
            {renderCaramel()}
            {renderFruit()}
            {renderFlower()}
          </Grid>
        );
      },
      clearQualities = () => {
        //  setVarietal(null);
        setMouthFeel(null);
        setAcidity(null);
        setCaramel(null);
        setFruit(null);
        setFlower(null);
      },
      onNo = () => {
        clearQualities();
        history.push(`/learning?coffee=${varietal.key}`);
      },
      onYes = () => {
        commit();
      },
      commit = () => {
        const { key } = varietal;
        addLearningItem({
          variables: {
            item: {
              mouthfeel,
              acidity,
              caramel,
              fruit,
              flower,
              key,
            },
          },
        });
      };
    useEffect(() => {
      if (!isStable()) {
        history.push(`/learning?coffee=${varietal.key}`);
      }
    }, [varietal]);
    return isStable() ? (
      <div className={classes.page}>
        <Grid container className={classes.header}>
          <BackButton />
        </Grid>
        <HeaderImg size="small" />
        <Grid
          container
          direction="column"
          spacing={0}
          wrap="nowrap"
          className={classes.content}
        ></Grid>
        <Typography className={classes.heading}>Your Choices</Typography>
        {renderResults()}
        <Grid className={classes.buttons} container>
          <Button
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={onNo}
          >
            No, let's start over
          </Button>
          <Button
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={onYes}
          >
            Yes, these are my choices
          </Button>
        </Grid>
      </div>
    ) : null;
  };
export default Profile;
