import React, { useGlobal, useState, useEffect } from 'reactn';
import useStyles from './styles';
import Typography from '@material-ui/core/Typography';
import {
  Grid,
  IconButton,
  Dialog,
  DialogActions,
  DialogTitle,
  Button,
} from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import MorePricesIcon from '@material-ui/icons/AddCircleOutlineOutlined';
import BackButton from '../../../backButton';
import Sidebar from '../../../sidebar';
import UserButtons from '../../user/buttons';
import ShareButton from '../../share/button';
import ShoppingCartButton from '../../buy/shoppingCart/button';
import Purchase from '../purchase';
import classNames from 'classnames';
import HeaderImg from '../../../headerImg';
import MenuBar from '../../../menuBar';
import decafIcon from '../../../../images/decaf.png';
import notDecafIcon from '../../../../images/notDecaf.png';
import Copyright from '../../../copyright';
import { useHistory, useLocation } from 'react-router-dom';

import {
  REGULAR_BRAIN,
  DECAF_BRAIN,
  DECAF,
  MOUTHFEEL,
  ACIDITY,
  CARAMEL,
  FRUIT,
  FLOWER,
  FLAVORS,
  QUALITIES,
  BLEND_SHOPPING_CART,
  COFFEES,
  CUSTOM_SETTINGS,
} from '../../../../constant/coffee';
import ImgLight from '../blend/images/light.png';
import ImgFrench from '../blend/images/french.png';
import ImgMedium from '../blend/images/medium.png';
import ImgVienna from '../blend/images/vienna.png';
import { mouthfeelLookup } from '../mouthfeel/steps';
import { acidityLookup } from '../acidity/steps';
import { caramelLookup } from '../caramel/steps';
import { flowerLookup } from '../flower/steps';
import { fruitLookup } from '../fruit/steps';
const roundToNearest10 = (x) => Math.round(x / 10) * 10;
const MAX_ROAST_HEIGHT = 200;
const Blend = (props) => {
  const location = useLocation();
  const classes = useStyles();
  const [coffees] = useGlobal(COFFEES);
  const settings = useGlobal(CUSTOM_SETTINGS);
  const history = useHistory();
  const [acidity] = useGlobal(ACIDITY);
  const [decaf] = useGlobal(DECAF);
  const [mouthFeel] = useGlobal(MOUTHFEEL);
  const [caramel] = useGlobal(CARAMEL);
  const [fruit] = useGlobal(FRUIT);
  const [flower] = useGlobal(FLOWER);
  const [purchases, setPurchases] = useGlobal(BLEND_SHOPPING_CART);
  const [selectedRoasts, setSelectedRoasts] = useState([]);
  const [showErrorDlog, setShowErrorDlog] = useState(false);
  const [blendId, setBlendId] = useState();
  const background = {
    light: ImgLight,
    french: ImgFrench,
    medium: ImgMedium,
    vienna: ImgVienna,
  };
  useEffect(() => {
    if (coffees) {
      try {
        const blendId = new URLSearchParams(location.search).get('blendid');
        const roasts = blendId.split('_').map((x) => {
          const y = x.split('-');
          const coffee = getCoffee(y[0]);
          const roast = {
            coffeeId: coffee.key,
            key: coffee.key,
            percentage: parseInt(y[1]),
          };

          return {
            coffeeId: coffee.key,
            key: coffee.key,
            percentage: parseInt(y[1]),
          };
        });
        const bId = blendId.replaceAll('-', ',').replaceAll('_', ':');
        setBlendId(bId);
        setSelectedRoasts(roasts);
      } catch (error) {
        setShowErrorDlog(true);
      }
    }
    // "13,50:21,40:26,10:"   13-50_21-40_26-10
  }, [coffees]);

  const getCoffee = (key) => {
    const k = parseInt(key);
    return coffees.find((c) => parseInt(c.key) === k);
  };
  const requester = (key) => {
    const item = getCoffee(key);
    return `m:${item[MOUTHFEEL]},  a:${item[ACIDITY]},  c:${item[CARAMEL]},  fr:${item[FRUIT]},  fl:${item[FLOWER]}`;
  };
  const isStable = () => {
    return mouthFeel && acidity && caramel && fruit && flower;
  };
  const adjustPercentages = (roasts) => {
    let total = roasts.reduce((a, v) => {
      return a + v.value;
    }, 0);
    roasts.forEach((c) => {
      c.percentage = Math.ceil((c.value / total) * 100) / 1;
    });
    if (roasts[2].percentage <= 10) {
      roasts[0].percentage = roundToNearest10(
        Math.ceil(((roasts[0].value + roasts[2].value) / total) * 100) / 1
      );
      roasts[1].percentage = 100 - roasts[0].percentage;
      roasts[2].percentage = 0;
    } else if (roasts[0].percentage <= 30 && roasts[1].percentage <= 30) {
      roasts[0].percentage = 40;
      roasts[1].percentage = 40;
      roasts[2].percentage = 20;
    } else {
      roasts[2].percentage =
        100 - (roasts[0].percentage + roasts[1].percentage);
    }
    return roasts.filter((r) => r.percentage);
  };
  const handleErrorDlogButton = () => {
    setShowErrorDlog(false);
    history.push('/');
  };
  const renderErrorDlog = () => {
    if (showErrorDlog == true) {
      return (
        <Dialog
          open={showErrorDlog}
          onClose={() => setShowErrorDlog(false)}
          aria-labelledby="alert-dialog-title"
        >
          <DialogTitle id="remove-dialog-title">
            There was a problem with your request.
          </DialogTitle>
          <DialogActions>
            <Button onClick={handleErrorDlogButton} color="primary">
              ok
            </Button>
          </DialogActions>
        </Dialog>
      );
    }
    return null;
  };

  const renderDecaf = (isDecaf) => {
    return (
      <span className={classes.decafContainer}>
        {isDecaf ? (
          <Typography className={classes.decafText}>decaf</Typography>
        ) : null}
        <img
          className={classes.decafIcon}
          src={isDecaf ? decafIcon : notDecafIcon}
        />
      </span>
    );
  };
  const renderMouthfeel = (mouthfeel) => {
    const sorted = Object.entries(
      mouthfeelLookup.run({ step: mouthfeel })
    ).sort((a, b) => b[1] - a[1]);
    return (
      <Typography
        varient="h6"
        className={classNames(classes.capitalize, classes.attribute)}
      >
        {`Mouthfeel is ${sorted[0][0]}`}
      </Typography>
    );
  };
  const renderAcidity = (acidity) => {
    const sorted = Object.entries(acidityLookup.run({ step: acidity })).sort(
      (a, b) => b[1] - a[1]
    );
    return (
      <Typography
        varient="h6"
        className={classNames(classes.capitalize, classes.attribute)}
      >
        {`Acidity is ${sorted[0][0]}`}
      </Typography>
    );
  };
  const renderCaramel = (caramel) => {
    const sorted = Object.entries(caramelLookup.run({ step: caramel })).sort(
      (a, b) => b[1] - a[1]
    );
    return (
      <Typography
        varient="h6"
        className={classNames(classes.capitalize, classes.attribute)}
      >
        {`Caramel flavor is ${sorted[0][0]}`}
      </Typography>
    );
  };
  const renderFlower = (flower) => {
    const sorted = Object.entries(flowerLookup.run({ step: flower })).sort(
      (a, b) => b[1] - a[1]
    );
    return (
      <Typography
        varient="h6"
        className={classNames(classes.capitalize, classes.attribute)}
      >
        {`Has ${sorted[0][0]} floral notes`}
      </Typography>
    );
  };
  const renderFruit = (fruit) => {
    const sorted = Object.entries(fruitLookup.run({ step: fruit })).sort(
      (a, b) => b[1] - a[1]
    );
    return (
      <Typography
        varient="h6"
        className={classNames(classes.capitalize, classes.attribute)}
      >
        {`Has ${sorted[0][0]} fruitiness`}
      </Typography>
    );
  };
  const renderAttributes = (coffee) => {
    return (
      <Grid container className={classes.qualities}>
        <Grid container className={classes.attributes}>
          {renderDecaf(coffee[DECAF])}
          {renderMouthfeel(coffee[MOUTHFEEL])}
          {renderAcidity(coffee[ACIDITY])}
          {renderCaramel(coffee[CARAMEL])}
          {renderFruit(coffee[FRUIT])}
          {renderFlower(coffee[FLOWER])}
        </Grid>
        {renderNotableQualities(coffee)}
        {renderNotableFlavors(coffee)}
      </Grid>
    );
  };
  const renderProfile = (coffee) => {
    return (
      <div className={classes.coffeeProfile}>
        {MOUTHFEEL}:{coffee[MOUTHFEEL]},&nbsp;&nbsp;{ACIDITY}:{coffee[ACIDITY]}
        ,&nbsp;&nbsp;
        {CARAMEL}:{coffee[CARAMEL]},&nbsp;&nbsp;{FRUIT}:{coffee[FRUIT]}
        ,&nbsp;&nbsp;{FLOWER}:{coffee[FLOWER]}
      </div>
    );
  };
  const renderNotableQualities = (coffee) => {
    let flavors = '';
    const flavs = coffee[QUALITIES];
    for (let index = 0; ; index++) {
      if (index === flavs.length) {
        flavors += '.';
        break;
      } else if (index === flavs.length - 1) {
        flavors += `, and ${flavs[index]}`;
      } else if (index === 0) {
        flavors += `${flavs[index]}`;
      } else {
        flavors += `, ${flavs[index]}`;
      }
    }
    return (
      <Typography
        className={classNames(classes.capitalize, classes.attribute)}
        variant="body1"
      >
        {`Special qualities are ${flavors}`}
      </Typography>
    );
  };
  const renderNotableFlavors = (coffee) => {
    let flavors = '';
    const flavs = coffee[FLAVORS];
    for (let index = 0; ; index++) {
      if (index === flavs.length) {
        flavors += '.';
        break;
      } else if (index === flavs.length - 1) {
        flavors += `, and ${flavs[index]}`;
      } else if (index === 0) {
        flavors += `${flavs[index]}`;
      } else {
        flavors += `, ${flavs[index]}`;
      }
    }
    return (
      <Typography
        className={classNames(classes.capitalize, classes.attribute)}
        variant="body1"
      >
        {`Notable flavors are ${flavors}`}
      </Typography>
    );
  };
  const renderRoastDesc = () => {
    return (
      <Grid container className={classes.desc}>
        {selectedRoasts.map((r, i) => {
          const coffee = getCoffee(r.key);
          return (
            <Grid container className={classes.descs} key={i}>
              <Typography variant="body1">
                <span
                  className={classNames(
                    classes.topBeginning,
                    classes.capitalize
                  )}
                >
                  {`${r.percentage}% ${coffee.roast} ${coffee.region}`}
                </span>
                {coffee.paragraphs[0]}
              </Typography>
              {coffee.paragraphs[1] ? (
                <Typography className={classes.paragraph} variant="body1">
                  {coffee.paragraphs[1]}
                </Typography>
              ) : null}
              {renderAttributes(coffee)}
              {renderProfile(coffee)}
            </Grid>
          );
        })}
      </Grid>
    );
  };
  const renderRoasts = () => {
    return selectedRoasts && selectedRoasts.length ? (
      <Grid container className={classes.roasts}>
        {selectedRoasts.map((r, i) => {
          const coffee = getCoffee(r.key),
            multiplier = MAX_ROAST_HEIGHT / selectedRoasts[0].percentage,
            className =
              r.percentage < 20
                ? classNames(classes.roastBase, classes.roastShort)
                : classNames(classes.roastBase, classes.roast);
          return (
            <Paper
              elevation={4}
              className={className}
              key={i}
              style={{
                width: `${100 / selectedRoasts.length + 1}%`,
                height: `${r.percentage * multiplier}px`,
                backgroundImage: `url("${background[coffee.roast]}")`,
              }}
            >
              <Typography
                className={classNames(classes.roastRegion, classes.capitalize)}
                variant="h6"
              >{`${coffee.roast} ${coffee.region}`}</Typography>
              <Typography
                className={classNames(classes.roastPercentage)}
                variant="h6"
              >
                {r.percentage ? `${r.percentage}%` : null}
              </Typography>
            </Paper>
          );
        })}
      </Grid>
    ) : null;
  };
  const onPurchaseChange = (event) => {
    const { type, purchaseId, price, quantity } = event;
    const _purchases = [...purchases];
    const purchase = _purchases.find(
      (purchase) => purchase.purchaseId === purchaseId
    );
    if (purchase) {
      purchase.quantity = quantity;
      purchase.price = price;
    } else {
      _purchases.push({
        coffees: selectedRoasts,
        // qualities: {
        //   mouthFeel,
        //   acidity,
        //   caramel,
        //   flower,
        //   fruit,
        // },
        blendId,
        quantity,
        price,
        purchaseId,
        quantity,
      });
    }
    setPurchases(_purchases);
  };
  const onDeleteClick = (purchaseId) => {
    const filtered = purchases.filter(
      (purchase) => purchase.purchaseId !== purchaseId
    );
    setPurchases(filtered);
  };
  const onAddClick = () => {
    if (purchases.find((p) => p.quantity === 0)) return;

    const { prices } = settings[0];
    const currentId = generateBlendId();
    const _purchases = purchases.filter(
      (p) => generateBlendId(p.blend) === currentId
    );
    const count = _purchases.length;
    const purchaseId = `${currentId}-${count}`;
    purchases.push({
      coffees: selectedRoasts,
      qualities: {
        mouthFeel,
        acidity,
        caramel,
        flower,
        fruit,
      },
      quantity: 0,
      purchaseId,
      price: prices[0],
    });
    setPurchases(purchases);
  };
  const generateBlendId = (blend = selectedRoasts) => {
    const id = blend.reduce((previousValue, currentValue) => {
      const { key, percentage } = currentValue;
      const blendId = `${key},${percentage}:`;
      return previousValue + blendId;
    }, '');
    return id;
  };
  const renderPurchases = () => {
    if (settings && purchases && blendId) {
      const blendPurchases = purchases.filter((purchase) => {
        return purchase.blendId === blendId;
      });
      const { prices } = settings[0];
      const newPurchases = blendPurchases.map((p, i) => (
        <Purchase
          key={i}
          purchaseId={p.purchaseId}
          quantity={p.quantity}
          price={p.price}
          onDelete={onDeleteClick}
          onPurchaseChange={onPurchaseChange}
        />
      ));
      const emptyPurchase = (
        <Purchase
          key={Math.random()}
          purchaseId={`${generateBlendId()}-${0}`}
          quantity={0}
          price={prices[0]}
          onDelete={onDeleteClick}
          onPurchaseChange={onPurchaseChange}
        />
      );
      return (
        <div className={classes.purchases}>
          {newPurchases.length > 0 ? newPurchases : emptyPurchase}
          <IconButton
            className={classes.addButton}
            size="small"
            color="primary"
            onClick={() => onAddClick()}
          >
            <MorePricesIcon />
          </IconButton>
        </div>
      );
    } else return null;
  };
  const failure = () => {
    history.push('/customblend');
    return null;
  };

  //   useEffect(() => {
  //     if (selectedRoasts && selectedRoasts.length > 0) {
  //       setBlendId(generateBlendId(selectedRoasts));
  //     }
  //   }, [selectedRoasts, setBlendId]);
  //   useEffect(() => {
  //     run();
  //   }, [regularBrain, decafBrain]);
  //   useEffect(() => {
  //     if (!isStable()) {
  //       failure();
  //     }
  //   }, [isStable]);

  return (
    <div className={classes.page}>
      {renderErrorDlog()}
      <Grid container className={classes.header}>
        <div>
          <Sidebar />
        </div>
        <ShoppingCartButton />
        <UserButtons />
        <ShareButton baseStyle={classes.ShareButton} />
      </Grid>
      <div className={classes.menubar}>
        <BackButton goTo="/customblend/" />
        <MenuBar dept="custom" />
      </div>
      <HeaderImg size="small" />
      <Grid
        container
        direction="column"
        spacing={0}
        wrap="nowrap"
        className={classes.content}
      >
        {renderRoasts()}
        {renderPurchases()}

        {renderRoastDesc()}
      </Grid>
      <Copyright />
    </div>
  );
};
export default Blend;
