import React, { useGlobal } from 'reactn';
import { useHistory } from 'react-router-dom';
export default function useApolloError() {
  const history = useHistory();
  const [_openError_, setOpenError] = useGlobal('error');
  const [_errorMsg_, setErrorMsg] = useGlobal('errormessage');
  function handleError(err) {
    debugger;
    if (err && err.message) {
      const { message } = err;
      console.log(message);
      debugger;
      if (message.endsWith('400')) {
        setOpenError(true);
        setErrorMsg('session timed out please signin');
        //setUser(null);
        history.push('/user/signin');
      } else if (message === 'Failed to fetch') {
        setOpenError(true);
        setErrorMsg(
          'there was a server error. Please try again in a few minutes'
        );
      } else {
        setOpenError(true);
        setErrorMsg(message);
      }
    }
  }
  return handleError;
}
