import React, { useGlobal, useEffect, useState } from 'reactn';
import useStyles from './styles';
import { useHistory, useLocation } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import HeaderImg from '../../headerImg';
import text from './text';
import BackButton from '../../backButton';
import { VARIETAL } from '../../../constant/learning';
import { COFFEES } from '../../../constant/coffee';
import BeansImg from '../../../images/beans.png';
import Sidebar from '../../sidebar';
const Learning = (props) => {
  const classes = useStyles();
  const [varietal, setVarietal] = useGlobal(VARIETAL);
  const [coffees] = useGlobal(COFFEES);
  const [coffeeId, setCoffeeId] = useState();
  const history = useHistory();
  const location = useLocation();
  useEffect(() => {
    if (location) {
      let searchParams = new URLSearchParams(location.search);
      setCoffeeId(searchParams.get('coffee'));
    }
  }, [location]);
  useEffect(() => {
    if (coffees && coffeeId) {
      const coffee = coffees.find((c) => c.key === coffeeId);
      if (coffee) {
        setVarietal(coffee);
      } else {
        console.log('coffee not found');
        debugger;
      }
      //  setVarietals(result);
    }
  }, [coffees, coffeeId]);
  return varietal ? (
    <div className={classes.page}>
      <Sidebar />
      <BackButton />
      <HeaderImg size="small" />
      <Grid
        container
        direction="column"
        className={classes.content}
        align="center"
      >
        {/* renderSelectVarietal() */}
        <Typography paragraph className={classes.topDesc} variant="body1">
          {text.learning.top}
        </Typography>
        <img
          alt="beans"
          src={BeansImg}
          role="img"
          aria-label="bright"
          className={classes.img}
        />
        <Typography className={classes.locx} variant="body1">
          {varietal && text.learning.command}
        </Typography>
        {varietal && (
          <Button
            color="primary"
            fullWidth={false}
            className={classes.startButton}
            variant="contained"
            size="large"
            onClick={() => {
              history.push('/learning/mouthfeel');
            }}
          >
            Start tasting with {`${varietal.region}-${varietal.roast}`}
          </Button>
        )}
        <Typography className={classes.locx} variant="body1">
          {text.learning.bottom}
        </Typography>
      </Grid>
    </div>
  ) : null;
};
export default Learning;
