import React, { useEffect, useGlobal } from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import { useHistory } from 'react-router-dom';
import BruuImg from '../../images/header-bruu.png';
import CaCImg from '../../images/header-cac.png';
import PeetImg from '../../images/header-peets.png';
import PbImg from '../../images/header-pb.png';
import BeanBoxImg from '../../images/header-beanbox.png';
import classNames from 'classnames';
const HeaderImg = (props) => {
  const classes = useStyles();
  const [site, setSite] = useGlobal('site');
  const history = useHistory();
  const renderImg = () => {
    switch (site) {
      case 'br':
        return (
          <img
            src={BruuImg}
            alt="img"
            onClick={() => {
              history.push('/');
            }}
            className={classNames(classes.img, classes.imgBruu)}
          />
        );
      case 'pt':
        return (
          <img
            src={PeetImg}
            alt="img"
            onClick={() => {
              history.push('/');
            }}
            className={classNames(classes.img, classes.imgPeet)}
          />
        );
      case 'cc':
        return (
          <img
            src={CaCImg}
            alt="img"
            onClick={() => {
              history.push('/');
            }}
            className={classNames(classes.img, classes.imgCaC)}
          />
        );
      case 'bb':
        return (
          <img
            src={BeanBoxImg}
            alt="img"
            onClick={() => {
              history.push('/');
            }}
            className={classNames(classes.img, classes.imgBB)}
          />
        );
      default:
        return (
          <img
            src="/images/header-pb.png"
            alt="img"
            onClick={() => {
              history.push('/');
            }}
            className={classNames(classes.img, classes.imgPb)}
          />
        );
    }
  };
  useEffect(() => {
    if (!site) {
      setSite(history.location.search.substring(6, 10));
    }
  }, [site, history]);
  return (
    <Grid container className={classes.container}>
      {renderImg()}
    </Grid>
  );
};
export default HeaderImg;
