import React, { useGlobal, useState, useEffect } from 'reactn';
import Paper from '@material-ui/core/Paper';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import MenuItem from '@material-ui/core/MenuItem';
import CircularProgress from '@material-ui/core/CircularProgress';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import { useHistory, useLocation } from 'react-router-dom';
import BackButton from '../../../backButton';
import useUserLoginError from '../../user/useLoginError'; //'../../../../utils/useUserLoginError';
import text from '../text';
import {
  USER,
  PAYMENT,
  BLEND_SHOPPING_CART,
} from '../../../../constant/coffee';
import HeaderImg from '../../../headerImg';
import { useLazyQuery, gql } from '@apollo/client';
import Copyright from '../../../copyright';

const VALIDATE_CC = gql`
  query validateCard($creditcard: CreditCardInput) {
    validateCreditcard(creditcard: $creditcard) {
      name
      year
      month
      number
      cvc
      user_id
    }
  }
`;
const cards = [
    ['amex', '^3[47][0-9]{13}$', '/svg/payment/amex.svg'],
    ['discover', '^6(?:011|5[0-9]{2})[0-9]{12}$', '/svg/payment/discover.svg'],
    ['mastercard', '^5[1-5][0-9]{14}$', '/svg/payment/mastercard.svg'],
    ['visa', '^4[0-9]{12}(?:[0-9]{3})?$', '/svg/payment/visa.svg'],
  ],
  getCCImageSrc = (name) => {
    const result = cards.find((c) => c[0] == name);
    return result ? result[2] : null;
  },
  Payment = (props) => {
    const userLoginError = useUserLoginError();
    const classes = useStyles();
    const history = useHistory();
    const location = useLocation();
    const [user] = useGlobal(USER);
    const [payment, setPayment] = useGlobal(PAYMENT);
    const [doValidateCC, { loading, error, data }] = useLazyQuery(VALIDATE_CC);
    const [name, setName] = useState('ron rey');
    const [showProgress, setShowProgress] = useState(false);
    const [number, setNumber] = useState('5392770008804032');
    const [month, setMonth] = useState('2');
    const [year, setYear] = useState('2022');
    const [cvc, setCvc] = useState('2234');
    const [card, setCard] = useState(null);
    const [showDialog, setShowDialog] = useState(false);
    const [dialogMsg, setDialogMsg] = useState('test');
    const onNumberChange = (event) => {
      setNumber(event.target.value);
    };
    const onMonthChange = (event) => {
      setMonth(event.target.value);
    };
    const onYearChange = (event) => {
      setYear(event.target.value);
    };
    const onCvcChange = (event) => {
      setCvc(event.target.value);
    };
    const onNameChange = (event) => {
      setName(event.target.value);
    };
    const onContinue = () => {
      setShowProgress(true);
      doValidateCC({
        variables: {
          creditcard: {
            name: name,
            number: number,
            month: month,
            year: year,
            cvc: cvc,
            user_id: user._id,
          },
        },
      });
    };
    const renderCCIcons = () => {
      return (
        <Grid container className={classes.ccIconContainer}>
          {cards.map((c, i) => (
            <img alt="" key={i} className={classes.ccIcon} src={c[2]} />
          ))}
        </Grid>
      );
    };
    const renderDialog = () => {
      return showDialog ? (
        <Dialog
          open={showDialog}
          onClose={() => showDialog(false)}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{'Alert'}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {dialogMsg}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={() => setShowDialog(false)}
              color="primary"
              autoFocus
            >
              Agree
            </Button>
          </DialogActions>
        </Dialog>
      ) : null;
    };
    const renderContinueButton = () => {
      return testCVC() && testYear() && testMonth() && testNumber() ? (
        <Button
          fullWidth={true}
          variant="contained"
          color="primary"
          size="large"
          className={classes.button}
          onClick={onContinue}
        >
          continue...
        </Button>
      ) : null;
    };
    const testCVC = () => {
      return cvc && cvc;
    };
    const testName = () => {
      return name && name.length > 2;
    };
    const testMonth = () => {
      return month && month !== 0;
      //return /^\d{5}(?:[-\s]\d{4})?$/.test(zipcode);
    };
    const testYear = () => {
      return year && year !== 0;
      //return /^\d{5}(?:[-\s]\d{4})?$/.test(zipcode);
    };
    const testNumber = () => {
      if (!number) return false;
      const result = cards.find((c) => number.match(c[1]));
      return result ? result[0] : null;
    };
    const onClickOtherPay = (type) => {
      console.log(type);
    };
    const renderExpiration = () => {
      return (
        <>
          {renderDialog()}
          <Paper elevation={2} className={classes.monthYear}>
            <Typography variant="subtitle1" style={{ marginRight: 8 }}>
              {text.buy.payment.expiration}
            </Typography>
            <TextField
              id="month"
              className={classes.select}
              select
              error={!testMonth()}
              label={text.buy.payment.month}
              value={month}
              onChange={onMonthChange}
              helperText="exp month"
            >
              <MenuItem key={0} value={'0'}></MenuItem>
              <MenuItem key={1} value={'1'}>
                1
              </MenuItem>
              <MenuItem key={2} value={'2'}>
                2
              </MenuItem>
              <MenuItem key={3} value={'3'}>
                3
              </MenuItem>
              <MenuItem key={4} value={'4'}>
                4
              </MenuItem>
              <MenuItem key={5} value={'5'}>
                5
              </MenuItem>
              <MenuItem key={6} value={'6'}>
                6
              </MenuItem>
              <MenuItem key={7} value={'7'}>
                7
              </MenuItem>
              <MenuItem key={8} value={'8'}>
                8
              </MenuItem>
              <MenuItem key={9} value={'9'}>
                9
              </MenuItem>
              <MenuItem key={10} value={'10'}>
                10
              </MenuItem>
              <MenuItem key={11} value={'11'}>
                11
              </MenuItem>
              <MenuItem key={12} value={'12'}>
                12
              </MenuItem>
            </TextField>
            <TextField
              id="year"
              select
              className={classes.select}
              error={!testYear()}
              label={text.buy.payment.year}
              value={year}
              onChange={onYearChange}
              helperText="exp year"
            >
              <MenuItem value={0}></MenuItem>
              <MenuItem value={'2021'}>2021</MenuItem>
              <MenuItem value={'2022'}>2022</MenuItem>
              <MenuItem value={'2023'}>2023</MenuItem>
              <MenuItem value={'2024'}>2024</MenuItem>
              <MenuItem value={'2025'}>2025</MenuItem>
              <MenuItem value={'2026'}>2026</MenuItem>
              <MenuItem value={'2027'}>2027</MenuItem>
              <MenuItem value={'2028'}>2028</MenuItem>
              <MenuItem value={'2029'}>2029</MenuItem>
              <MenuItem value={'2030'}>2030</MenuItem>
              <MenuItem value={'2031'}>2031</MenuItem>
              <MenuItem value={'2032'}>2032</MenuItem>
            </TextField>
          </Paper>
        </>
      );
    };
    const renderCC = () => {
      return (
        <Paper elevation={2} className={classes.creditcard}>
          <TextField
            required
            autoComplete="cc-name"
            type="text"
            error={!testName()}
            value={name}
            label={text.buy.payment.name}
            className={classes.textField}
            margin="normal"
            onChange={onNameChange}
          />
          <Grid container className={classes.ccNumberContainer}>
            {renderCCIcons()}
            <Grid container className={classes.ccNumberInput}>
              <TextField
                required
                autoComplete="cc-number"
                style={{ flexGrow: 3 }}
                error={!testNumber()}
                value={number}
                label={text.buy.payment.number}
                className={classes.textField}
                margin="normal"
                onChange={onNumberChange}
              />
              {card ? (
                <img
                  alt="cc img"
                  className={classes.ccImg}
                  src={getCCImageSrc(card)}
                />
              ) : null}
            </Grid>
          </Grid>
          {renderExpiration()}
          <TextField
            required
            autoComplete="cc-csc"
            error={!testCVC()}
            value={cvc}
            label={text.buy.payment.cvc}
            className={classes.cvc}
            margin="normal"
            onChange={onCvcChange}
          />
        </Paper>
      );
    };
    const renderOtherPayMethods = () => {
      return (
        <Grid container className={classes.otherPay}>
          <Button
            variant="contained"
            className={classes.otherPayButton}
            onClick={() => onClickOtherPay('paypal')}
          >
            <img
              alt=""
              className={classes.ccImg}
              src="/svg/payment/paypal.svg"
            />
          </Button>
          <Button
            variant="contained"
            className={classes.otherPayButton}
            onClick={() => onClickOtherPay('applePay')}
          >
            <img
              alt=""
              className={classes.ccImg}
              src="/svg/payment/apple-pay.svg"
            />
          </Button>
          <Button
            variant="contained"
            className={classes.otherPayButton}
            onClick={() => onClickOtherPay('googlePay')}
          >
            <img
              alt=""
              className={classes.ccImg}
              src="/svg/payment/googlepay.svg"
            />
          </Button>
        </Grid>
      );
    };
    // useEffect(() => {
    //   setCard(testNumber());
    // }, [number]);
    useEffect(() => {
      if (!user) {
        history.push('/');
      }
    }, []);
    useEffect(() => {
      if (data && data.validateCreditcard) {
        setPayment(data.validateCreditcard);
        history.push(`/buy/finalize`);
      }
    }, [data]);
    useEffect(() => {
      if (loading) setShowProgress(true);
    }, [loading]);
    useEffect(() => {
      if (error) {
        debugger;
        setShowProgress(false);
        setPayment(null);
      }
    }, [error]);
    return (
      <>
        {error ? userLoginError(error) : null}
        {showProgress && <CircularProgress className={classes.progress} />}
        <div className={classes.page}>
          <Grid container className={classes.header}>
            <BackButton />
          </Grid>
          <HeaderImg size="small" />
          <Grid container className={classes.paymentContainer}>
            {renderContinueButton()}

            {renderOtherPayMethods()}
            {renderCC()}
          </Grid>
        </div>
        <Copyright />
      </>
    );
  };
export default Payment;
