import React, {useGlobal,useEffect} from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {useHistory} from "react-router-dom";
import BackButton from '../../../backButton';
import {MOUTHFEEL,ACIDITY,VARIETAL} from '../../../../constant/learning';
import sliderValues from './steps';
import Quality from '../quality';
import text from '../text';
const Acidity=(props)=>{
	const classes = useStyles(),
	[mouthFeel]= useGlobal(MOUTHFEEL),
	[varietal]= useGlobal(VARIETAL),
	[acidity, setAcidity]= useGlobal(ACIDITY),
	history=useHistory(),
 	onClick=(acidity)=>{
		history.push('/learning/caramel')
		setAcidity(acidity)
	};

	useEffect(()=>{
		if(!varietal||!mouthFeel){
			history.push('/learning');
		};
	},[]);
	return (
		<div className={classes.page}>
		<Grid container className={classes.header}>
			<BackButton />
			</Grid>
			<Typography
				variant='h5'
				gutterBottom
				align='center'>
					{varietal&&`${varietal.region}-${varietal.roast}`}
			</Typography>
		<Quality
			value={acidity}
			title='🚰 Acidity 🍋'
			text={text.acidity}
			values={sliderValues}
			onClick={onClick}/>

		</div>)
}
export default Acidity;
