const Address = (props) => {
  const { onValid, initial, prefix } = props;
  const classes = useStyles();
  const [street, setStreet] = useState(
    initial && initial.street ? initial.street : ''
  );
  const [suite, setSuite] = useState(
    initial && initial.suite ? initial.suite : ''
  );
  const [zipcode, setZipcode] = useState(
    initial && initial.zipcode ? initial.zipcode : ''
  );
  const [city, setCity] = useState(initial && initial.city ? initial.city : '');
  const [state, setState] = useState(
    initial && initial.state ? initial.state : ''
  );
  const [country, setCountry] = useState(
    initial && initial.country ? initial.country : ''
  );
  const onStreetChange = (event) => {
    setStreet(event.target.value);
  };
  const onCountryChange = (event) => {
    setCountry(event.target.value);
  };
  const onSuiteChange = (event) => {
    setSuite(event.target.value);
  };
  const onZipcodeChange = (event) => {
    setZipcode(event.target.value);
  };
  const onCityChange = (event) => {
    setCity(event.target.value);
  };
  const onStateChange = (event) => {
    setState(event.target.value);
  };
  const testStreet = () => {
    return street && street.length > 2;
  };
  const testCountry = () => {
    const test = countries.find((s) => s.value === country);
    return test && test.value != '';
  };
  const testCity = () => {
    return city && city.length > 2;
  };
  const testZipcode = () => {
    return zipcode && /^\d{5}(?:[-\s]\d{4})?$/.test(zipcode);
  };
  const testState = () => {
    const test = states.find((s) => s.value === state);
    return test && test.value != '';
  };
  const testAll = () => {
    return (
      testStreet() &&
      testCountry() &&
      testCity() &&
      testZipcode() &&
      testState()
    );
  };
  const valid = () => {
    if (onValid) {
      if (testAll()) {
        onValid({ street, suite, zipcode, city, state, country });
      } else {
        onValid(null);
      }
    }
  };
  const renderStateMenuItems = () => {
    const r = states.map((item) => (
      <MenuItem key={item.value} value={item.value}>
        {item.name}
      </MenuItem>
    ));

    return r;
  };
  const renderCountriesMenuItems = () => {
    const r = countries.map((item) => (
      <MenuItem key={item.value} value={item.value}>
        {item.name}
      </MenuItem>
    ));

    return r;
  };
  const renderState = () => {
    return (
      <TextField
        required
        name={text.address.state}
        autoComplete={`${prefix} address-level1`}
        id={text.address.state}
        error={!testState()}
        value={state}
        onChange={onStateChange}
        label={text.address.state}
        select={true}
        className={classes.textField}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <StateIcon />
            </InputAdornment>
          ),
        }}
      >
        {renderStateMenuItems()}
      </TextField>
    );
  };
  const renderCountry = () => {
    return (
      <TextField
        required
        error={!testCountry()}
        autoComplete={`${prefix} country`}
        value={country}
        onChange={onCountryChange}
        label={text.address.country}
        select={true}
        className={classes.textField}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <CountryIcon />
            </InputAdornment>
          ),
        }}
      >
        {renderCountriesMenuItems()}
      </TextField>
    );
  };
  const renderInputs = () => {
    return (
      <Paper elevation={2} className={classes.inputs}>
        <Typography variant="h6">Address</Typography>
        <TextField
          autoComplete={`${prefix} street-address`}
          error={!testStreet()}
          value={street}
          label={text.address.street}
          className={classes.textField}
          margin="normal"
          onChange={onStreetChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <StreetIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          autoComplete={`${prefix} address-line2`}
          value={suite}
          label={text.address.suite}
          className={classes.textField}
          margin="normal"
          onChange={onSuiteChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SuiteIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          autoComplete={`${prefix} address-level2`}
          required
          error={!testCity()}
          value={city}
          label={text.address.city}
          className={classes.textField}
          margin="normal"
          onChange={onCityChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <CityIcon />
              </InputAdornment>
            ),
          }}
        />
        {renderState()}
        <TextField
          autoComplete={`${prefix} postal-code`}
          error={!testZipcode()}
          value={zipcode}
          label={text.address.zipcode}
          className={classes.textField}
          margin="normal"
          onChange={onZipcodeChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <ZipcodeIcon />
              </InputAdornment>
            ),
          }}
        />
        {renderCountry()}
      </Paper>
    );
  };
  useEffect(() => {
    valid();
  }, [street, suite, zipcode, city, state, country]);
  useEffect(() => {
    if (initial) {
      const { street, suite, zipcode, city, state, country } = initial;
      street && setStreet(street);
      suite && setSuite(suite);
      zipcode && setZipcode(zipcode);
      city && setCity(city);
      street && setState(state);
      country && setCountry(country);
    }
  }, []);
  return (
    <div className={classes.page}>
      <Grid
        container
        direction="column"
        spacing={0}
        wrap="nowrap"
        className={classes.content}
      >
        {renderInputs()}
      </Grid>
    </div>
  );
};
export default Address;
