import React, { setGlobal, useGlobal, useEffect } from 'reactn';
import CssBaseline from '@material-ui/core/CssBaseline';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
} from '@material-ui/core';
import primaryColor from '@material-ui/core/colors/lightBlue';
import secondaryColor from '@material-ui/core/colors/grey';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { BrowserRouter } from 'react-router-dom';
import Routing from '../routing';
import './styles.css';
import Grid from '@material-ui/core/Grid';
import useData from '../hook/useData';
import {
  MOUTHFEEL,
  ACIDITY,
  CARAMEL,
  FRUIT,
  FLOWER,
  CURRENT_BLEND,
  USER,
  BLEND_SHOPPING_CART,
  COFFEES_SHOPPING_CART,
  OUR_BLENDS_SHOPPING_CART,
  PAYMENT,
} from '../../constant/coffee';
const App = (props) => {
  useData();
  const [openError, setOpenError] = useGlobal('error');
  const [errorMsg, setErrorMsg] = useGlobal('errormessage');
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)'),
    theme = React.useMemo(
      () =>
        createMuiTheme({
          typography: {
            fontFamily: '"SFRegular", "Helvetica", "Arial", sans-serif',
            fontSize: 13,
          },
          palette: {
            type: prefersDarkMode ? 'dark' : 'light',
            primary: {
              light: primaryColor[50],
              main: prefersDarkMode ? primaryColor['700'] : primaryColor['700'],
              //	dark: primaryColor[400],
              contrastText: prefersDarkMode
                ? secondaryColor[200]
                : secondaryColor[100],
            },
            secondary: {
              light: prefersDarkMode ? secondaryColor[200] : secondaryColor[50],
              main: prefersDarkMode ? secondaryColor[200] : secondaryColor[300],
              contrastText: prefersDarkMode
                ? primaryColor['A700']
                : primaryColor['A700'],
            },
          },
        }),
      [prefersDarkMode]
    ),
    handleClose = () => {
      setOpenError(false);
    },
    renderErrorDialog = () => {
      return (
        <Dialog
          open={openError !== undefined ? openError : false}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">
            We encountered an error
          </DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {errorMsg}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary" autoFocus>
              thank you
            </Button>
          </DialogActions>
        </Dialog>
      );
    };
  useEffect(() => {
    setGlobal({
      [MOUTHFEEL]: null,
      [ACIDITY]: null,
      [CARAMEL]: null,
      [FRUIT]: null,
      [FLOWER]: null,
      [CURRENT_BLEND]: null,
      [USER]: null,
      [PAYMENT]: null,
      [BLEND_SHOPPING_CART]: [],
      [COFFEES_SHOPPING_CART]: [],
      [OUR_BLENDS_SHOPPING_CART]: [],
    });
  }, []);

  return (
    <BrowserRouter>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        {renderErrorDialog()}
        <Grid container direction="column">
          <Routing />
        </Grid>
      </ThemeProvider>
    </BrowserRouter>
  );
};
export default App;
