import React from 'reactn';
import {Switch, Route, Redirect, withRouter, } from "react-router-dom";
import nav from './nav';

function Router(props) {
	return (
	<Switch>
		{nav.map((r, i)=>(<Route key={i} path={r.to} exact component={r.component} />))}
		<Redirect to='/' />
	</Switch>)
}
export default (withRouter(Router));
