import React, { useGlobal, useEffect } from 'reactn';
import brain from 'brain.js';
import { useQuery, gql } from '@apollo/client';
import {
  REGULAR_BRAIN,
  DECAF_BRAIN,
  COFFEES,
  OUR_BLENDS,
  CUSTOM_SETTINGS,
} from '../../constant/coffee';
import UseApolloError from '../handleError/useApolloError';
const SETTINGS = gql`
  query GetCustomSettings {
    getCustomSettings {
      prices {
        measurement
        price
        quantity
      }
    }
  }
`;
const BRAIN = gql`
  query getBrain {
    getBrain {
      regular
      decaf
    }
  }
`;
const COFFEE = gql`
  query getCoffees {
    getCoffees {
      _id
      key
      price
      decaf
      prices {
        key
        measurement
        quantity
        price
      }
      mouthfeel
      acidity
      caramel
      fruit
      flower
      flavors
      qualities
      region
      roast
      paragraphs
    }
  }
`;

const GET_OUR_BLENDS = gql`
  query GetOurBlends {
    getOurBlends {
      _id
      name
      decaf
      createdAt
      updatedAt
      price
      prices {
        measurement
        quantity
        price
      }
      coffees
      mouthfeel
      acidity
      caramel
      fruit
      flower
      flavors
      qualities
      paragraphs
    }
  }
`;

const useData = () => {
  const apolloError = UseApolloError();
  const { error: errorBrain, data: dataBrain } = useQuery(BRAIN);
  const { error: errorOurBlends, data: ourBlendsData } =
    useQuery(GET_OUR_BLENDS);
  const { error: errorCoffee, data: dataCoffee } = useQuery(COFFEE);
  const { error: errorCustomSettings, data: dataCustomSettings } =
    useQuery(SETTINGS);
  const [_decafBrain_, setDecafBrain] = useGlobal(DECAF_BRAIN);
  const [regularBrain, setRegularBrain] = useGlobal(REGULAR_BRAIN);
  const [coffees, setCoffees] = useGlobal(COFFEES);
  const [ourBlends, setOurBlends] = useGlobal(OUR_BLENDS);
  const [customSettings, setCustomSettings] = useGlobal(CUSTOM_SETTINGS);
  useEffect(() => {
    if (dataBrain && !regularBrain) {
      const { regular, decaf } = dataBrain.getBrain;
      const decafBrain = new brain.NeuralNetwork();
      decafBrain.fromJSON(JSON.parse(decaf));
      setDecafBrain(decafBrain);
      const regularBrain = new brain.NeuralNetwork();
      regularBrain.fromJSON(JSON.parse(regular));
      setRegularBrain(regularBrain);
    }
  }, [dataBrain]);
  useEffect(() => {
    if (ourBlendsData && !ourBlends) {
      setOurBlends(
        ourBlendsData.getOurBlends ? ourBlendsData.getOurBlends : []
      );
    }
  }, [ourBlendsData]);
  useEffect(() => {
    if (dataCoffee && !coffees) {
      setCoffees(dataCoffee.getCoffees);
    }
  }, [dataCoffee]);

  useEffect(() => {
    if (dataCustomSettings && !customSettings) {
      const { prices } = dataCustomSettings.getCustomSettings;
      const newSettings = { prices: prices };
      setCustomSettings(newSettings);
    }
  }, [dataCustomSettings]);

  useEffect(() => {
    if (errorBrain) {
      apolloError(errorBrain);
    } else if (errorCoffee) {
      apolloError(errorCoffee);
    } else if (errorOurBlends) {
      apolloError(errorOurBlends);
    } else if (errorCustomSettings) {
      apolloError(errorCustomSettings);
    }
  }, [errorBrain, errorCoffee, errorOurBlends, errorCustomSettings]);
};

export default useData;
