import React, { useGlobal, useEffect } from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import { useHistory } from 'react-router-dom';
import BackButton from '../../../backButton';
import Sidebar from '../../../sidebar';
import UserButtons from '../../user/buttons';
import ShoppingCartButton from '../../buy/shoppingCart/button';
import {
  MOUTHFEEL,
  ACIDITY,
  CARAMEL,
  FRUIT,
} from '../../../../constant/coffee';
import sliderValues from './steps';
import Quality from '../quality';
import text from '../text';
import HeaderImg from '../../../headerImg';
import MenuBar from '../../../menuBar';
import Copyright from '../../../copyright';

const Fruit = (props) => {
  const classes = useStyles();
  const history = useHistory();
  const [mouthFeel] = useGlobal(MOUTHFEEL);
  const [acidity] = useGlobal(ACIDITY);
  const [caramel] = useGlobal(CARAMEL);
  const [fruit, setFruit] = useGlobal(FRUIT);
  const onClick = (fruit) => {
    history.push('/customblend/flower');
    setFruit(fruit);
  };
  useEffect(() => {
    if (!mouthFeel || !acidity || !caramel) {
      history.push('/customblend');
    }
  }, []);
  return (
    <div className={classes.page}>
      <Grid container className={classes.header}>
        <div>
          <Sidebar />
        </div>
        <ShoppingCartButton />
        <UserButtons />
      </Grid>
      <div className={classes.menubar}>
        <BackButton />
        <MenuBar dept="custom" />
      </div>
      <HeaderImg size="small" />
      <Quality
        value={fruit}
        title={text.fruit.title}
        text={text.fruit}
        values={sliderValues}
        onClick={onClick}
      />
      <Copyright />
    </div>
  );
};
export default Fruit;
