import React, { useGlobal, useState } from 'reactn';
import useStyles from './styles';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import { useHistory } from 'react-router-dom';
import { USER } from '../../../../constant/coffee';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import text from '../text';
const UserButtons = (props) => {
  const classes = useStyles();
  const [user, setUser] = useGlobal(USER);
  const [dialogOpen, setDialogOpen] = useState(false);
  const history = useHistory();
  const joinOnClick = () => {
    history.push('/user/join');
  };
  const signInOnClick = () => {
    if (user) setDialogOpen(true);
    else history.push('/user/signin');
  };
  const handleDlogClose = () => {
    setDialogOpen(false);
  };
  const onSigninOut = () => {
    setDialogOpen(false);
    setUser(null);
    localStorage.removeItem('auth_token');
  };
  const renderDialog = () => {
    return (
      <Dialog
        open={dialogOpen}
        onClose={handleDlogClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="join-dialog-description"
      >
        <DialogTitle id="join-dialog-title">{`${text.buttonComponent.title} ${
          user ? user.name : '?'
        }`}</DialogTitle>
        <DialogActions>
          <Button onClick={handleDlogClose} color="primary">
            Cancel
          </Button>
          <Button onClick={onSigninOut} color="primary">
            {text.buttonComponent.siginoutButton}
          </Button>
        </DialogActions>
      </Dialog>
    );
  };
  return (
    <>
      {renderDialog()}
      <ButtonGroup className={classes.buttonGroup} aria-label="button group">
        {!user ? (
          <Button
            onClick={joinOnClick}
            variant="outlined"
            color="primary"
            className={classes.joinButton}
            size="small"
          >
            {text.buttonComponent.joinButton}
          </Button>
        ) : null}
        <Button
          onClick={signInOnClick}
          variant="outlined"
          color="primary"
          className={classes.signInButton}
          size="small"
        >
          {!user
            ? text.buttonComponent.buttonTextSignin
            : text.buttonComponent.buttonTextSignout}
        </Button>
      </ButtonGroup>
    </>
  );
};
export default UserButtons;
