import React, { useGlobal, useEffect } from 'reactn';
import useStyles from './styles';
import Grid from '@material-ui/core/Grid';
import { useHistory } from 'react-router-dom';
import { MOUTHFEEL, DECAF } from '../../../../constant/coffee';
import sliderValues from './steps';
import Quality from '../quality';
import BackButton from '../../../backButton';
import Sidebar from '../../../sidebar';
import UserButtons from '../../user/buttons';
import ShoppingCartButton from '../../buy/shoppingCart/button';
import Copyright from '../../../copyright';

import text from '../text';
import HeaderImg from '../../../headerImg';
import MenuBar from '../../../menuBar';
const Mouthfeel = (props) => {
  const classes = useStyles();
  const [mouthFeel, setMouthFeel] = useGlobal(MOUTHFEEL);
  const [decaf] = useGlobal(DECAF);
  const history = useHistory();
  const onClick = (mf) => {
    history.push('/customblend/acidity');
    setMouthFeel(mf);
  };
  useEffect(() => {
    if (decaf === undefined) {
      history.push('/customblend');
    }
  }, [decaf]);
  return (
    <div className={classes.page}>
      <Grid container className={classes.header}>
        <div>
          <Sidebar />
        </div>
        <ShoppingCartButton />
        <UserButtons />
      </Grid>
      <div className={classes.menubar}>
        <BackButton />
        <MenuBar dept="custom" />
      </div>
      <HeaderImg size="small" />
      <Quality
        value={mouthFeel}
        title={text.mouthfeel.title}
        text={text.mouthfeel}
        values={sliderValues}
        onClick={onClick}
      />
      <Copyright />
    </div>
  );
};
export default Mouthfeel;
